    // via /src/utils/createContent.js
    export default {"info":{"title":"النكبة\\n الجزء الأول \"خيوط المؤامرة\"","paramsArray":[["الناشر: الجزيرة"]]},"content":[{"title":"1","type":"richMedia","params":["hiddenContent/001","s0-344"],"content":"","id":"001"},{"title":"2","type":"richMedia","params":["hiddenContent/001","s344-615"],"content":"","id":"002"},{"title":"3","type":"richMedia","params":["hiddenContent/001","s615-905"],"content":"","id":"003"},{"title":"4","type":"richMedia","params":["hiddenContent/001","s905-1261"],"content":"","id":"004"},{"title":"5","type":"richMedia","params":["hiddenContent/001","s1261-1531"],"content":"","id":"005"},{"title":"6","type":"richMedia","params":["hiddenContent/001","s1531-1835"],"content":"","id":"006"},{"title":"7","type":"richMedia","params":["hiddenContent/001","s1835-2109"],"content":"","id":"007"},{"title":"8","type":"richMedia","params":["hiddenContent/001","s2109-2394"],"content":"","id":"008"},{"title":"9","type":"richMedia","params":["hiddenContent/001","s2394-2715"],"content":"","id":"009"},{"title":"10","type":"richMedia","params":["hiddenContent/001","s2715-2940"],"content":"","id":"010"}],"hiddenContent":[{"title":"Nakbah-1-movie","type":"richMedia","params":["https://www.youtube.com/watch?v=fbcZhO-QJQU"],"content":{"phrases":[{"id":"000","start":0,"end":0,"text":""},{"id":"001","start":23.9,"end":26.6,"text":"النكبة"},{"id":"002","start":27.83,"end":32.03,"text":"الجزء الأول خيوط المؤامرة"},{"id":"003","start":32.43,"end":41.89,"text":"\"إن مأساة فلسطين ليست مأساة محلية، إنها مأساة العالم لأنها ظلم يهدد السلام العالمي\".","voiceName":"أرنولد توبيني. المؤرخ البريطاني العالمي"},{"id":"004","start":61.15,"end":64.45,"text":"عكا"},{"id":"005","start":65.75,"end":69.85,"text":"لم تبدء نكبة ثمانية و أربعون قبل ستين عاما،"},{"id":"006","start":69.95,"end":73.35,"text":"لقد بدأت سياسيا قبل أكثر من مئتي عام."},{"id":"007","start":73.35,"end":81.1,"text":"عام ١٧٩٩ إستعصت هذه الأسوار، هنا في عكا على نابليون"},{"id":"008","start":81.3,"end":84.85,"text":"فاتجه تفكيره الإستعماري ضد توسع بريطانيا"},{"id":"009","start":84.85,"end":91.85,"text":"ووجه نداء إلى يهود العالم:"},{"id":"010","start":92.55,"end":94.4,"text":"\"أيها الإسرائيليون،"},{"id":"011","start":94.4,"end":98.15,"text":"انهضوا! فهذه هي اللحظة المناسبة،"},{"id":"012","start":98.15,"end":103.3,"text":"إن فرنسا تقدم لكم يدها الآن حاملة إرث إسرائيل،"},{"id":"013","start":103.3,"end":108.65,"text":"سارعوا للمطالبة باستعادة مكانتكم بين شعوب العالم\"."},{"id":"014","start":109.7,"end":114.7,"text":"تحول نداء نابليون إلى خبر رئيسي في الصحف الفرنسية."},{"id":"015","start":120.6,"end":125.2,"text":"هزم نابليون ولم يبقى من ذكراه في عكا سوى مجسما"},{"id":"016","start":125.2,"end":128.15,"text":"يقف على تلة سميت باسمه،"},{"id":"017","start":131.75,"end":136.95,"text":"لكن فكرة نابليون بإنشاء وجود يهودي في المنطقة لم تمت."},{"id":"018","start":136.95,"end":140.45,"text":"بعد أربعين عاما أعادت بريطانيا الفكرة"},{"id":"019","start":140.45,"end":145.1,"text":"لترد على محاولة محمد علي توحيد مصر وسوريا"},{"id":"020","start":145.1,"end":153.45,"text":"عام ١٨٤٠ كتب وزير الخارجية البريطاني بالميريستون إلى سفيره في إسطنبول:"},{"id":"021","start":155.25,"end":158.3,"text":"\"عليك أن تقنع السلطان وحاشيته،"},{"id":"022","start":158.3,"end":165.35,"text":"بأن الحكومة الإنجليزية ترى أن الوقت أصبح مناسبا لفتح فلسطين أمام هجرة اليهود\"."},{"id":"023","start":166.4,"end":171.4,"text":"لم يزد عدد اليهود في فلسطين آنذاك عن ثلاثة آلاف."},{"id":"024","start":172.9,"end":179.65,"text":"إستجاب اليهود للمبادرة البريطانية وكان من الأوائل البارون الثري إدموند روتشلد،"},{"id":"025","start":179.65,"end":184.55,"text":"زار فلسطين أربعة مرات للإطلاع على فرص الإستثمار فيها،"},{"id":"026","start":184.6,"end":190.85,"text":"ومول بأكثر من أربعة عشر مليون فرنك إنشاء ثلاثين مستعمرة يهودية"},{"id":"027","start":190.85,"end":193.85,"text":"من أهمها مستعمرة ريشون ليتسيون."},{"id":"028","start":199.3,"end":202.2,"text":"التي رفعت هذا العلم الخاص بها"},{"id":"029","start":202.2,"end":208.15,"text":"عام ١٨٨٥ بينما فلسطين تحت الحكم العثماني."},{"id":"030","start":210,"end":216.25,"text":"اليوم يركض رفات البارون روتشيلد بضريح قرب حيفا، يزوره الأطفال الإسرائيليون"},{"id":"031","start":216.25,"end":221.6,"text":"ليتعلموا عن ثري دعم إسرائيل قبل أكثر من مئة عام."},{"id":"032","start":232.85,"end":239.4,"text":"عام ١٨٨٥ ظهر لأول مره مصطلح الحركة الصهيونية"},{"id":"033","start":239.4,"end":243.3,"text":"علي يد الكاتب النمساوي ناتان بيرنباوم،"},{"id":"034","start":243.5,"end":246.8,"text":"وهدف الحركة الاستيطان في فلسطين،"},{"id":"035","start":247.05,"end":252.05,"text":"والصهيونية مشتقة من كلمة صهيون إحدى تلال القدس ."},{"id":"036","start":253.15,"end":257.55,"text":"طبعا كان في طائفة يهودية في البلاد تحت الحكم العثماني،","voiceName":"الدكتور هيليل كوهين مؤرخ و باحث في الجامعة العبرية"},{"id":"037","start":257.55,"end":262.3,"text":"لكنهم لم يكونوا صهاينة، هم كانوا يهود محليين،"},{"id":"038","start":262.3,"end":268.2,"text":"لكن اليهود القادمين من أوروبا خصوصا من شرق أوروبا في أواخر القرن التاسع عشر"},{"id":"039","start":268.4,"end":271.85,"text":"كانوا يريدون بناء يهودي جديد"},{"id":"040","start":273.25,"end":282.3,"text":"عام ١٨٩٦ نشر الصحفي الصهيوني ثيودور هيرتزل كتابه الدولة اليهودية باللغة الألمانية"},{"id":"041","start":284.65,"end":290.55,"text":"لكن يهود أوروبا كانوا يجدون حلمهم في الهجرة إلى أمريكا"},{"id":"042","start":290.75,"end":299.2,"text":"هذا ما جعل الطبيب ماكس نوردو الساعد الأيمن لـ هيرتزل يرسل اثنين من كبار رجال الدين اليهود إلي فلسطين."},{"id":"043","start":301.3,"end":305.7,"text":"أرسلا جوابا من سطر واحد جاء فيه:"},{"id":"044","start":306.45,"end":314.2,"text":"\"العروس جميلة جدا، ومستوفية لجميع الشروط، ولكنها متزوجة فعلا\""},{"id":"045","start":315.5,"end":321.1,"text":"و فهم نوردو المقصود هو أن فلسطين ليست كما ذكر هيرتزل أرض بلا شعب،"},{"id":"046","start":321.35,"end":325.3,"text":"وإن فيها شعبا يسكنها منذ آلاف السنين."},{"id":"047","start":329.35,"end":337.2,"text":"في صيف العام التالي ٢٩-٨-١٨٩٧ شارك نوردو وبيرنباوم تحت رئاسة هيرتزل في المؤتمر الصهيوني الأول في بازل السويسرية."},{"id":"048","start":337.5,"end":344.25,"text":"تبني المؤتمر برنامج تأسيس وطن معترف به للشعب اليهودي في فلسطين."},{"id":"049","start":346,"end":353.45,"text":"هيرتزل بدأ يدعو رسميا لإقامة هذا الكيان،","voiceName":"الدكتور أنيس صايغ رئيس تحرير الموسوعة الفلسطينية"},{"id":"050","start":353.45,"end":364.2,"text":"وكان على إتصال مع دول العالم الكبرى آنذاك، وفي أوروبا بشكل خاص حتى تحمي هذا الكيان."},{"id":"051","start":364.65,"end":371.7,"text":"هيرتزل هو أب الدولة الصهيونية بدون شك، لكنه ليس مبدع الفكر الصهيوني\"."},{"id":"052","start":372.25,"end":376.95,"text":"عادة الدول الكبرى يكون بينها تنافس ومصالح،","voiceName":"الصحفية روان الضامن"},{"id":"053","start":376.95,"end":383.9,"text":"كيف استطاعت الدول الأربعة الكبرى في ذلك الوقت أن تدعم جميعها فكرة الصهيونية؟"},{"id":"054","start":384.15,"end":390.35,"text":"هو لم يعقد مؤتمر لهذه الدول معا، وبالتالي إتفقت الدول على دعمه،","voiceName":"الدكتور أنيس صايغ رئيس تحرير الموسوعة الفلسطينية"},{"id":"055","start":390.5,"end":395.55,"text":"هو كان يثير التنافس بين دولة وأخرى، كان يكذب على دولة"},{"id":"056","start":395.75,"end":402.5,"text":"أني سوف أكون معكم إذا دعمتم دولة إسرائيل ضد الدول الأخرى،"},{"id":"057","start":402.7,"end":407.2,"text":"أنا أومن مصالحكم على مصالح الدول الأخرى\"."},{"id":"058","start":409.9,"end":413.8,"text":"أكدت إنجلترا على مصلحتها في قيام دولة يهودية"},{"id":"059","start":413.9,"end":417.05,"text":"عندما صرح رئيس وزراءها كامبل بنرمان"},{"id":"060","start":417.15,"end":426,"text":"بأنه من المهم إقامة حاجز قوي وغريب على الجسر الذي يربط البحر الأبيض مع البحر الأحمر، وأضاف:"},{"id":"061","start":427.3,"end":438.4,"text":"\"يتعين علينا أن نضع في هذه المنطقة وعلي مقربة من قناة السويس، قوة معادية لأهل البلد وصديقة للدول الأوروبية\"."},{"id":"062","start":438.4,"end":445.85,"text":"بحيث أن يكون هذا الجسم معتمد على الاستعمار الغربي و يعتمد على الأوروبيين.","voiceName":"الدكتور محسن صالح مدير مركز الزيتونة للأبحاث"},{"id":"063","start":445.85,"end":451.25,"text":"هذا الجسم ضمان بقائه أن يكون ما حوله ضعيفا"},{"id":"064","start":451.5,"end":461.05,"text":"في أيضا فكر عند الأوروبيين أن اليهود مقربين لهم أكثر من العرب، لذلك اقامة دولة يهودية يكون أفضل لهم","voiceName":"الدكتور هيليل كوهين مؤرخ و باحث في الجامعة العبرية"},{"id":"065","start":464.9,"end":475.1,"text":"عام ١٩٠٧ توجه إلى فلسطين لأول مره عالم الكيمياء البريطاني وعضو الحركة الصهيونية العالمية حاييم وايزمان،"},{"id":"066","start":475.1,"end":481.05,"text":"حتى يؤسس شركة تطوير آراضي فلسطين في يافا بدعم من عائلة روتشيلد،"},{"id":"067","start":481.2,"end":485.65,"text":"كان الهدف شراء أراضي فلسطين بطريقة منظمة."},{"id":"068","start":489,"end":492.55,"text":"بعد ثلاثة أعوام صفقة هامة تتحقق،"},{"id":"069","start":492.75,"end":499.45,"text":"إشترى الصندوق القومي اليهودي أكثر من مئتي ألف دونم في سهل مرج ابن عامر شمال فلسطين،"},{"id":"070","start":499.45,"end":507.65,"text":"كان البائع عائلة سُرسُق اللبنانية المقيمة في أوروبا، والتي إشترت الأرض مسبقا من ضباط عثمانيين،"},{"id":"071","start":507.65,"end":513.5,"text":"إشترطت الصفقة إخلاء الأراضي من آلاف الفلاحين المقيمين عليها."},{"id":"072","start":514.45,"end":519.55,"text":"أكثر من ستين ألف فلسطيني في منطقة مرج ابن عامر تم تهجيرهم من اراضيهم.","voiceName":"المحامي واكيم واكيم سكرتير جمعية الدفاع عن حقوق المهجرين"},{"id":"073","start":519.55,"end":524,"text":"إذا كانت النكبة تعني ترحيل الإنسان الفلسطيني من أرضه"},{"id":"074","start":524,"end":526.5,"text":"والاستيلاء عليها بالقوة،"},{"id":"075","start":526.6,"end":535.85,"text":"فان حقيقة النكبة ضمن هذين المفهومين، قد بدأت من عشرات السنين قبل سنه ١٩٤٨م\"."},{"id":"076","start":538.9,"end":544.65,"text":"أنه نوع أسوأ من الإستعمار مقارنة مع الإستعمار الأوروبي المعهود،","voiceName":"الصحفي ديفيد هيرست المتخصص في قضايا الشرق الأوسط يقول"},{"id":"077","start":545,"end":550,"text":"لأن الهدف لم يكن إستغلال أهل البلد بل طردهم"},{"id":"078","start":553.9,"end":561.3,"text":"من اللحظة الأولى التي استهدفت الحركة الصهيونية فلسطين كمكان لدولة يهودية مستقلة،","voiceName":"الدكتور إيلان بابيه، مؤرخ إسرائيلي - جامعة اكسترا- بريطانيا"},{"id":"079","start":561.3,"end":564.95,"text":"كان من الواضح أن هناك فلسطينيين على هذه الأرض."},{"id":"080","start":564.95,"end":574.15,"text":"لقد اقتنع الصهاينة بفكرة أن السبيل الوحيد لجعل فلسطين دولة يهودية، هي حمل الفلسطينيين على الرحيل"},{"id":"081","start":576,"end":580.95,"text":"اخلاء الفلاحين هو تطبيق للمبدئين: من ناحية، الإستيلاء على الأرض، ويسمى تهويد الأرض،","voiceName":"الدكتور عزمي بشارة مفكر عربي"},{"id":"082","start":581.15,"end":586.35,"text":"وثانيا: العمل العبري، وهو الإستعاضة عن الفلاحين العرب"},{"id":"083","start":586.55,"end":593.8,"text":"بيهود، إما من شرق أوروبا، وفي حالات أخرى عندما قل العدد إستعانوا بيهود من اليمن"},{"id":"084","start":594.35,"end":600.25,"text":"و تم تأسيس الحرس اليهودي \"هاشومير\" لحراسة المستعمرات اليهودية."},{"id":"085","start":608.4,"end":614.1,"text":"و تظاهر اليهود للمطالبة بالإعتراف باللغة العبرية في ظل الدولة العثمانية."},{"id":"086","start":616,"end":621.15,"text":"الانسان العربي والفلسطيني كان واعيا من اليوم الأول لمفهوم الصهيونية،","voiceName":"الدكتور مهدي عبد الهادي رئيس الجمعية الفلسطينية الأكاديمية للشؤون الدولية"},{"id":"087","start":621.75,"end":628.45,"text":"هي حركة عنصرية تبحث عن رأس مال لإستعمار أرض، وتوظيف البعد الديني"},{"id":"088","start":628.45,"end":634.4,"text":"لجعلها وطن لمن تبقي من يهود العالم، في هويه وكيان ومستقبل سياسي،"},{"id":"089","start":634.4,"end":639.85,"text":"هذا الوعي كان واضح جدا عند نجيب عزوري و نجيب نصار"},{"id":"090","start":639.85,"end":643.7,"text":"أصدر الصيدلاني نجيب نصار صحيفة \"الكرمل\" في حيفا"},{"id":"091","start":643.7,"end":646.65,"text":"بهدف التحذير من مطامع الصهيونية،"},{"id":"092","start":646.95,"end":653.3,"text":"وكان يؤكد أن الدولة اليهودية ستكون خنجرا ساما في خاصرة العرب."},{"id":"093","start":654.2,"end":660.05,"text":"كانت \"الكرمل\" صحفية صغيرة يعمل فيها نجيب نصار وزوجته ساذج بهائي،"},{"id":"094","start":660.05,"end":664.8,"text":"لم يسلم نصار من قمع الأتراك فسجن و لوحق وحورب."},{"id":"095","start":665.4,"end":680.7,"text":"1914 الحرب العالمية الاولى"},{"id":"096","start":681.15,"end":690.8,"text":"خلال الحرب كان البريطانيون يفكرون في موقع فلسطين،","voiceName":"الدكتور يوجين روغان رئيس مركز الشرق الأوسط- بريطانيا"},{"id":"097","start":690.8,"end":696,"text":"وانه على مقربه من قناة السويس ودور بريطانيا في مصر آنذاك،"},{"id":"098","start":696.2,"end":700.55,"text":"كان يدل على الدور الخاص لبريطانيا في فلسطين،"},{"id":"099","start":700.7,"end":710.7,"text":"ورأوا في اليهود والحركة الصهيونية شريك في امر استراتيجي استعماري"},{"id":"100","start":715.2,"end":723.5,"text":"عام ١٩١٥ قدمت لمجلس الوزراء البريطاني مذكرة سرية بعنوان (مستقبل فلسطين) ،"},{"id":"101","start":723.8,"end":730.15,"text":"كتبها أول صهيوني يهودي يصل لمنصب وزير بريطاني وهو هربرت صموئيل،"},{"id":"102","start":731.05,"end":732.3,"text":"جاء في الوثيقة:"},{"id":"103","start":733.88,"end":738.27,"text":"الوقت الحاضر ليس مناسبا لإنشاء دولة يهودية مستقلة،"},{"id":"104","start":738.3,"end":743.35,"text":"لذا يجب أن توضع فلسطين بعد الحرب تحت السيطرة البريطانية،"},{"id":"105","start":743.35,"end":750.52,"text":"لتعطي تسهيلات للمنظمات اليهودية لشراء الأراضي، وإقامة المستعمرات، وتنظيم الهجرة،"},{"id":"106","start":750.7,"end":756.92,"text":"وعلينا أن نزرع بين المحمديين ثلاثة إلى أربعة ملايين يهودي أوروبي"},{"id":"107","start":758.5,"end":765.45,"text":"تم الأخذ بتوصية صموئيل في الإتفاقية السرية التي جمعت بريطانيا وفرنسا لتقسيم سوريا الكبرى،"},{"id":"108","start":765.82,"end":768.32,"text":"عرفت الإتفاقية بإسم مهندسيها"},{"id":"109","start":768.5,"end":772.65,"text":"البريطاني- ماركس سايكس، والفرنسي- جورج بيكو،"},{"id":"110","start":773.05,"end":778.45,"text":"وضعت إتفاقية سايكس بيكو فلسطين تحت سيادة مشتركة للحلفاء"},{"id":"111","start":778.52,"end":781.35,"text":"لإعدادها للدولة اليهودية."},{"id":"112","start":781.35,"end":788.32,"text":"نحن من فلسطين، وفلسطين جزء من بلاد الشام. لسنا نحن من قسمها،","voiceName":"المواطن عمر الناطور مواليد يازور - قضاء يافا، يعيش في مخيم بلاطة- مدينه نابلس"},{"id":"113","start":789.02,"end":790.47,"text":"بل سايكس وبيكو،"},{"id":"114","start":792.46,"end":799.02,"text":"وبيكو، لقد سئمنا الانجليز والحديث عنهم، ولكن يجب أن نعرف،"},{"id":"115","start":799.12,"end":803.36,"text":"أن الذي قسم أرضنا، خطط حينها ليأتي باليهود إلى فلسطين\"."},{"id":"116","start":804.58,"end":808.14,"text":"كان سايكس صديقا مقربا من حاييم وايزمان"},{"id":"117","start":808.2,"end":812.74,"text":"وقد تبادلا عدة مراسلات، قبل توقيع إتفاقية سايكس بيكو،"},{"id":"118","start":813.06,"end":816.3,"text":"أوضحت هذه المراسلات دعم سايكس للحركة الصهيونية."},{"id":"119","start":819.74,"end":824.86,"text":"بعد عام واحد، وافق مجلس الوزراء البريطاني برئاسة ديفيد لويد جورج"},{"id":"120","start":825.02,"end":830.12,"text":"على إصدار وعد بريطاني لإنشاء وطن قومي لليهود في فلسطين،"},{"id":"121","start":830.5,"end":832.64,"text":"كتب الوعد على صيغة رسالة،"},{"id":"122","start":832.78,"end":835.02,"text":"وزير الخارجية آرثر بلفور"},{"id":"123","start":835.22,"end":836.72,"text":"إلى اللورد الصهيوني"},{"id":"124","start":836.82,"end":838.8,"text":"ليونيل ولتر روتشيلد."},{"id":"125","start":842.8,"end":845.6,"text":"\"بلفور كان يمثل الحكومة البريطانية،","voiceName":"الدكتور يوجين روغان رئيس مركز الشرق الأوسط - بريطانيا"},{"id":"126","start":845.76,"end":850.04,"text":"عندما كتب رسالته إلى اللورد روتشلد،"},{"id":"127","start":850.36,"end":852.84,"text":"كان يكتب بإسم"},{"id":"128","start":853.04,"end":854.72,"text":"الحكومة البريطانية،"},{"id":"129","start":854.74,"end":859.06,"text":"روتشلد كان يلعب دور شخص اللوبي"},{"id":"130","start":859.46,"end":862.78,"text":"حتى يشجع الحكومة البريطانية"},{"id":"131","start":862.8,"end":866.74,"text":"لأن تتخذ سياسة معينة بالنسبة"},{"id":"132","start":866.74,"end":870.98,"text":"لدور اليهود في فلسطين،"},{"id":"133","start":871.28,"end":874.52,"text":"فكان يمثل الحركة الصهيونية،"},{"id":"134","start":874.88,"end":877.28,"text":"وكان يقف من خلفهم جميعا حاييم وايزمان،"},{"id":"135","start":877.36,"end":883.96,"text":"الذي كان المسؤول عن المنظمة الدولية الصهيونية،"},{"id":"136","start":884.5,"end":887.6,"text":"والذي شارك في صياغة الوعد\"."},{"id":"137","start":888.56,"end":894.2,"text":"يقول: \"لم يكن لبريطانيا حق أخلاقي أو سياسي او قانوني","voiceName":"الدكتور آفي شليم مؤرخ إسرائيلي - جامعة أكسفورد – بريطانيا"},{"id":"138","start":894.16,"end":900.1,"text":"يخول لها أن تعد بإعطاء أرض هي ملك للعرب، لأناس أخرين،"},{"id":"139","start":900.42,"end":904,"text":"كان وعد بلفور غير أخلاقي و غير قانوني\"."},{"id":"140","start":908.58,"end":910.38,"text":"بعد شهر من وعد بلفور"},{"id":"141","start":910.38,"end":912.9,"text":"أحتفل في لندن بالإنجاز الصهيوني"},{"id":"142","start":913.04,"end":916.26,"text":"و تحدث في الإحتفال، اللورد روتشيلد"},{"id":"143","start":916.16,"end":920.52,"text":"وهيربرت صموئيل وماركس سايكس وحاييم وايزمن."},{"id":"144","start":923,"end":929.12,"text":"بعدها بأيام أحتل الجيش البريطاني بقيادة الجنرال أدموند"},{"id":"145","start":929.12,"end":931.12,"text":"القدس"},{"id":"146","start":941.96,"end":944.7,"text":"دخل مع جيش ألينبي فيلق يهودي،"},{"id":"147","start":944.84,"end":948.88,"text":"تم تدريبه بتوجيه من وزير المستعمرات البريطاني"},{"id":"148","start":948.88,"end":950.88,"text":"وينستون تشرتشيل"},{"id":"149","start":954.58,"end":960.42,"text":"كان أحد أعضاء هذا الفيلق وجه نعرفه جيدا بعد ذلك،"},{"id":"150","start":960.7,"end":962.78,"text":"ديفيد بن غوريون"},{"id":"151","start":967.92,"end":971.84,"text":"كان مع الفيلق أيضا زئيف جابوتنسكي"},{"id":"152","start":972.42,"end":975.44,"text":"وإدوين صموئيل ابن هيربرت صموئيل،"},{"id":"153","start":976.08,"end":977.56,"text":"نحميا رابين"},{"id":"154","start":977.5,"end":979.56,"text":"والد إسحاق رابين."},{"id":"155","start":987.56,"end":993.34,"text":"في غضون شهر إستقبل ألينبي في القدس صديقه الحميم حاييم وايزمن."},{"id":"156","start":998.32,"end":1003.56,"text":"كان عدد اليهود في فلسطين خمسين ألفا، مقابل نصف مليون عربي،"},{"id":"157","start":1003.56,"end":1006.56,"text":"أي شكل اليهود أقل من عشر السكان."},{"id":"158","start":1008.56,"end":1013.74,"text":"مع إنتهاء الحرب العالمية الأولى، وأثناء الإعداد لمؤتمر السلام في باريس،"},{"id":"159","start":1013.74,"end":1015.92,"text":"أرسل الرئيس الأمريكي ويلسون"},{"id":"160","start":1015.9,"end":1019.32,"text":"لجنة برئاسة الأكاديمي الدكتور هنري كنج"},{"id":"161","start":1019.36,"end":1021.32,"text":"والسياسي تشاس كرين"},{"id":"162","start":1021.32,"end":1023.66,"text":"إلى الشرق الأوسط، لدراسة الوضع"},{"id":"163","start":1024.08,"end":1026.94,"text":"وقدما تقريرا جاء في توصياته: الدقيقة"},{"id":"164","start":1027.56,"end":1031.14,"text":"إذا أردنا تطبيق مبادئ العدالة الأمريكية"},{"id":"165","start":1031.14,"end":1033.56,"text":"فإن أماني الشعب الفلسطيني"},{"id":"166","start":1033.56,"end":1037.18,"text":"هي التي يجب أن تقرر مستقبل فلسطين،"},{"id":"167","start":1037.14,"end":1042.32,"text":"إن تسعة أعشار السكان في فلسطين يعارضون برنامج الصهيوني،"},{"id":"168","start":1042.32,"end":1045.12,"text":"وهذا الشعور عام في جميع سوريا،"},{"id":"169","start":1045.12,"end":1048.24,"text":"لقد أكد كل مسؤول بريطاني قابلناه"},{"id":"170","start":1048.24,"end":1053.64,"text":"أن برنامج الصهيونية لا يمكن تنفيذه إلا بقوة السلاح،"},{"id":"171","start":1053.76,"end":1061.5,"text":"وأن هناك حاجة إلى ما لا يقل عن خمسين ألف جندي للبدء بتنفيذ هذا البرنامج،"},{"id":"172","start":1061.5,"end":1066.16,"text":"و هذا بحد ذاته دليل على ظلم برنامج الصهيونية،"},{"id":"173","start":1066.16,"end":1073.62,"text":"و بناء على كل ما سبق، نوصي بالتخلي عن مشروع جعل فلسطين كومنوليث يهودي\"."},{"id":"174","start":1077.34,"end":1081.66,"text":"لم يجد تقرير كينج كرين آذانا صاغية."},{"id":"175","start":1091.48,"end":1096.7,"text":"شارك في مؤتمر السلام عن بريطانيا ديفيد ليودجورج و آرثر بلفور"},{"id":"176","start":1097.1,"end":1100.58,"text":"وترأس الوفد الصهيوني حاييم وايزمن،"},{"id":"177","start":1100.6,"end":1104.74,"text":"ليعرض خريطة توضح مساحة الوطن القومي لليهود"},{"id":"178","start":1105.04,"end":1112.24,"text":"كانت تشمل فلسطين وغور شرق الأردن وجنوب لبنان وصولا إلى صيدا وصور"},{"id":"179","start":1112.24,"end":1114.36,"text":"وكذلك القنيطرة السورية،"},{"id":"180","start":1114.72,"end":1119.22,"text":"بحدود تسير بمحاذاة خط سكة الحديد الحجازية"},{"id":"181","start":1119.98,"end":1123.1,"text":"في المؤتمر أحاط ألينبي وديفيد ليودجورج"},{"id":"182","start":1123.1,"end":1125.32,"text":"الأمير فيصل ابن الحسين"},{"id":"183","start":1126.42,"end":1132.34,"text":"ووقع وايزمان مع فيصل اتفاقية عرفت بإسم فيصل وايزمن،"},{"id":"184","start":1138.32,"end":1142.12,"text":"لكن مهندسها الحقيقي كان الكولونيل البريطاني"},{"id":"185","start":1142.12,"end":1146.16,"text":"توماس إدوارد لورنس، المعروف بلقب لورنس العرب،"},{"id":"186","start":1147.64,"end":1153.82,"text":"قع فيصل لكنه تحفظ بخطه أن الإتفاقية لن تتم إلا إذا"},{"id":"187","start":1153.82,"end":1156.4,"text":"نالت العرب استقلاله."},{"id":"188","start":1162.62,"end":1166.44,"text":"الحركة الصهيونية منذ سنه ١٩١٩ تقريبا، فهمت","voiceName":"الدكتور هيلين كوهين مؤرخ و باحث في الجامعة العبرية"},{"id":"189","start":1166.82,"end":1171.88,"text":"أن التركيز على السياسة الخارجية لا يكفي."},{"id":"190","start":1171.9,"end":1181,"text":"نعم لنا الكثير من العلاقات في لندن، لنا مكتب في نيويورك أو واشنطن، ولنا انصار في برلين ..... الخ."},{"id":"191","start":1181.3,"end":1190.24,"text":"لمن يوجد هنا اناس يجب أخذهم بعين الإعتبار،"},{"id":"192","start":1190.68,"end":1194.56,"text":"في سنه ١٩١٩ أسسوا مركز استخباري"},{"id":"193","start":1194.72,"end":1198.86,"text":"للحركة الصهيونية،"},{"id":"194","start":1199,"end":1206.66,"text":"وكانت العلاقات جيدة في الكثير من المناطق بين اليهود والعرب،"},{"id":"195","start":1206.8,"end":1220.4,"text":"وكان هناك يهود ممن هاجر ذويهم عام ١٨٨٤ وهم ولدوا في فلسطين، وكانوا يتحدثون العربية ويركبون الخيل"},{"id":"196","start":1220.34,"end":1222.74,"text":"ويذهبون عند العرب، ولهم أصدقاء من العرب."},{"id":"197","start":1222.74,"end":1229.3,"text":"أصبحوا يجمعون معلومات ذات طابع سياسي،"},{"id":"198","start":1229.3,"end":1231.3,"text":"ما هو الرأي العام،"},{"id":"199","start":1231.3,"end":1235.04,"text":"هل سيوافقون على المشروع الصهيوني أو لن يوافقوا،"},{"id":"200","start":1235.24,"end":1242.52,"text":"أين توجد آراضي غير مأهولة، وهل يوجد اشخاص ممن يريدون يبع أراضيهم لليهود،"},{"id":"201","start":1242.78,"end":1258.08,"text":"وايضاً إذا ما كانت هناك خطط لمهاجمة اليهود من منطقة معينة او مجموعة معينه\"."},{"id":"202","start":1261.76,"end":1267,"text":"عام ١٩٢٠ عين أول حاكم بريطاني لفلسطين،"},{"id":"203","start":1267.36,"end":1272.16,"text":"ووقع اختيار الحكومة البريطانية لمنصب المندوب السامي البريطان"},{"id":"204","start":1272.34,"end":1275.24,"text":"علي اليهودي الصهيوني هيربرت صموئيل."},{"id":"205","start":1275.64,"end":1279.7,"text":"جاء صموئيل ليطبق ما اقترحه قبل ذلك بخمسة أعوام،"},{"id":"206","start":1279.7,"end":1283.72,"text":"وهو تهيئة فلسطين لتكون دولة يهودية."},{"id":"207","start":1284.9,"end":1291.7,"text":"كان صموئيل صديقا لألينبي و تدل مراسلاتهما على تفاهم كامل على الدور البريطاني."},{"id":"208","start":1292.38,"end":1299.8,"text":"كانت المادة الثانية من بنود صُك الإنتداب البريطاني الذي صادقت عليه عصبة الأمم تنص على:"},{"id":"209","start":1302,"end":1307.78,"text":"تكليف بريطاني لوضع البلاد في حاله سياسية وإدارية وإقتصادية"},{"id":"210","start":1308,"end":1311.9,"text":"تسمح بإنشاء الوطن القومي اليهودي\"."},{"id":"211","start":1312.02,"end":1318.08,"text":"و منذ اليوم الأول اعتبر صموئيل العبرية لغة رسمية إلي جانب العربية والإنجليزية"},{"id":"212","start":1318.6,"end":1323.1,"text":"وأضيف إلى كلمة فلسطين بالعبرية الحرفان آلف (أ) وياء (ي)"},{"id":"213","start":1323.1,"end":1325.84,"text":"كرمز إلى كلمتي إيريتس يسرائيل."},{"id":"214","start":1325.84,"end":1327.92,"text":"إي أرض إسرائيل."},{"id":"215","start":1332.02,"end":1337.98,"text":"هيربرت صموئيل تحت إشراف الإنتداب البريطاني، هو الذي خلق إسرائيل","voiceName":"الدكتور سلمان أبو سته مؤلف أطلس فلسطين ٤٨"},{"id":"216","start":1338.42,"end":1345.94,"text":"قانونيا. اولا: وضع القوانين، حوالي مئة قانون لكي يمكن تسريب الأراضي العربية إلى يد اليهود."},{"id":"217","start":1346.2,"end":1353.4,"text":"ثانيا: سمح لليهود بأن يكون لهم نظام تعليم منفصل عن نظام حكومة فلسطين،"},{"id":"218","start":1353.58,"end":1359.06,"text":"وبنوا أيضا نواة وزارة الطاقة، لأنهم أنشأوا محطات طاقة لهم"},{"id":"219","start":1359.22,"end":1362.46,"text":"وايضا وزارة الأشغال ووزارة المياه"},{"id":"220","start":1362.72,"end":1370.24,"text":"وأهم ما عمله الإنجليز أنهم سمحو لهم بأن يكون لهم جيش منفصل،"},{"id":"221","start":1370.22,"end":1373.08,"text":"أنا أتكلم عن ١٩٢٠\""},{"id":"222","start":1373.36,"end":1378.38,"text":"وهيربرت صموئيل هو نفسه يقول، أنا سوف أعمل على تطبيق روح","voiceName":"يوسف حجازي صحفي و باحث من غزة"},{"id":"223","start":1378.38,"end":1383.4,"text":"تصريح بلفور، بن غوريون يقول عنه أنه اليهودي الصالح،"},{"id":"224","start":1384.62,"end":1387.46,"text":"ووايزمن يقول نحن الذين عيناه\"."},{"id":"225","start":1388.04,"end":1393.4,"text":"لقد وفرت لهم بريطانيا الوسيلة للهجرة،","voiceName":"الصحفي ديفيد هيرست متخصص في قضايا الشرق الأوسط"},{"id":"226","start":1393.4,"end":1397.72,"text":"وما كانوا ليستطيعوا الهجرة لولا الوجود البريطاني،"},{"id":"227","start":1398.28,"end":1405.14,"text":"فالمعركة الحاسمة في المراحل الأولى كانت إدخال اليهود إلى فلسطين والاستحواذ على الأرض،"},{"id":"228","start":1405.36,"end":1416.28,"text":"لم يكن لليهود أن يفعلوا ذلك دون رعاية من الحكومة البريطانية\""},{"id":"229","start":1416.54,"end":1422.12,"text":"الإنتداب البريطاني أخذ أهم شيئ، من المشاع والمتروك والمملوك للحاكم","voiceName":"الدكتور مهدي عبد الهادي رئيس الجمعية الفلسطينية الأكاديمة للشؤون الدولية"},{"id":"230","start":1422.48,"end":1425.36,"text":"وملكها للحركة الصهيونية في المستوطنات فورا\"."},{"id":"231","start":1425.6,"end":1431.48,"text":"\"السلطة البريطانية كانت تعطي منح أراضي لليهود حتى يبنوا عليها الكيبوتسات وغيرها،","voiceName":"الصحفي مسلم بسيسو المراسل وكالة لأسوشيتد برس في فلسطين عام ٤٨"},{"id":"232","start":1432.12,"end":1436.66,"text":"ونحن كنا نرى هذه الأشياء ولكن لم نعيها"},{"id":"233","start":1436.74,"end":1439.94,"text":"كان جيلي ذلك الوقت\"."},{"id":"234","start":1441.68,"end":1447.5,"text":"وحموا الوكالة اليهودية التي كانت شبه حكومة في ظل الإنتداب،","voiceName":"الدكتور أنيس صايغ رئيس تحرير الموسوعة الفلسطينية"},{"id":"235","start":1447.5,"end":1454.04,"text":"وأعطوها كل المساعدات العسكرية والمادية، وساعدوها على إخفاء نفسها،"},{"id":"236","start":1454.58,"end":1456.86,"text":"فهي مهارة صهيونية،"},{"id":"237","start":1457.06,"end":1458.86,"text":"ومؤامرة بريطانية،"},{"id":"238","start":1459.04,"end":1461.24,"text":"وإلي حد ما، وآسف أن أقول"},{"id":"239","start":1461.6,"end":1464.42,"text":"غباء أو بساطة عربية فلسطينية\"."},{"id":"240","start":1483.28,"end":1486.06,"text":"و مع التحيّز البريطاني لليهود"},{"id":"241","start":1486.06,"end":1490.02,"text":"و إزدياد عدد الفلاحين المطرودين من الأراضي الزراعية،"},{"id":"242","start":1490.2,"end":1494.18,"text":"بدأت مجموعات من الثوار في الظهور في المناطق الريفية،"},{"id":"243","start":1494.48,"end":1499.38,"text":"ونظمت مظاهرات حاشدة ضد هجرة الصهاينة عام ١٩٢١."},{"id":"244","start":1504.12,"end":1510.64,"text":"كانت القيادة السياسية الفلسطينية قيادة تقليدية قائمة علي توارث مناصب الزعماء،"},{"id":"245","start":1511.06,"end":1516.26,"text":"فمفتي القدس آمين الحسيني ورث هذا المنصب وعمره خمسة وعشرون عاما"},{"id":"246","start":1516.4,"end":1521.58,"text":"بعد وفاة أخيه كامل، الذي خلف والده طاهر الحسيني مفتي القدس،"},{"id":"247","start":1522.22,"end":1529.88,"text":"وتوجهت هذه القيادة منذ أوائل العشرينات في وفود متتالية إلى لندن لبحث القضية الفلسطينية"},{"id":"248","start":1532.64,"end":1540.08,"text":"الحركة الوطنية الفلسطينية كانت تعمل على أن تجمع الصيف والشتاء تحت سقف واحد، الحفاظ على صداقة بريطانيا من جانب،","voiceName":"يوسف حجازي صحفي و باحث من غزه"},{"id":"249","start":1540.64,"end":1543.92,"text":"ومعادة الحركة الصهيونية من جانب آخر، وهذا لا يمكن أن يستقيم\"."},{"id":"250","start":1543.92,"end":1550.66,"text":"نخبة دقيقة جدا، رقيقة جدا من حيث العدد والتعليم، يعني تقابل نخبة إمبراطوريات،","voiceName":"الدكتور عزمي بشارة - مفكر عربي"},{"id":"251","start":1550.98,"end":1556.76,"text":"اليهود في بدايات القرن العشرين كانوا نخب إمبراطوريات فعلا\"."},{"id":"252","start":1556.76,"end":1558.64,"text":"تقريبا في كل منطقة،","voiceName":"الدكتور هيليل كوهين مؤرخ و باحث في الجامعة العبري"},{"id":"253","start":1558.7,"end":1564.44,"text":"وفي كل لقاء للمؤسسات الفلسطينية التي أسست في تلك الفترة،"},{"id":"254","start":1566.02,"end":1575.6,"text":"على سبيل المثال، اللجنة المسيحية السلمية، والمجلس الإسلامي الأعلى، والهيئة الإسلامية وكل المؤسسات،"},{"id":"255","start":1575.96,"end":1578.66,"text":"بعد عدة ساعات من الجلسة،"},{"id":"256","start":1578.48,"end":1585.38,"text":"كان البريطانيين يقدمون تقارير لليهود بكل تفاصيل الاجتماع وما دار فيه، وكانوا يقدمون التوصيات بأن هذا عدو لكم،"},{"id":"257","start":1585.38,"end":1590.34,"text":"وهذا يمكن العمل معه أو ان تشترونه بالمال ... إلخ \"."},{"id":"258","start":1596.6,"end":1600.98,"text":"على الأرض، تواصل العمل لتحقيق هدف الإنتداب البريطاني"},{"id":"259","start":1601.16,"end":1605.08,"text":"وجاء في تقرير حكومة بريطانيا إلى مجلس عصبة الأمم"},{"id":"260","start":1605.28,"end":1611.28,"text":"عن إنجازات الإنتداب البريطاني خلال عام ١٩٢٥ أنه قد تم:"},{"id":"261","start":1611.84,"end":1619.74,"text":"أولا: تسهيل هجرة (33801) ثلاثة وثلاثون ألفا وثمانمائة وواحد يهودي، و تم منحهم الجنسية الفلسطينية،"},{"id":"262","start":1619.92,"end":1622.72,"text":"أي ثلاثة أضعاف عدد العام السابق؛"},{"id":"263","start":1623.24,"end":1627.04,"text":"ثانيا: إنشاء ثلاثة عشرة مستوطنة جديد؛"},{"id":"264","start":1627.74,"end":1633.38,"text":"ثالثا: تنظيم الهستدروت كنقابة للعمال اليهود بإدارة ديفيد بن غوريون؛"},{"id":"265","start":1633.78,"end":1639,"text":"رابعا: منح البلدة اليهودية تل أبيب استقلالا محليا؛"},{"id":"266","start":1639,"end":1646.7,"text":"خامسا: الافتتاح الرسمي للجامعة العبرية بحضور الجنرال أدموند الينبي، والحاكم البريطاني هيربرت صموئيل"},{"id":"267","start":1646.78,"end":1651.84,"text":"وضيف الشرف آرثر بلفور، ورئيس المنظمة الصهيونية العالمية"},{"id":"268","start":1651.84,"end":1653.2,"text":"حاييم وايزمن."},{"id":"269","start":1660.86,"end":1665.76,"text":"و حل بلفور ضيفا على وايزمان وزار عددا من المستعمرات اليهودية."},{"id":"270","start":1672.8,"end":1678.12,"text":"إلتقى في القدس صموئيل و بلفور و الينبي لتحديد الخطوة القادمة."},{"id":"271","start":1679.66,"end":1683.42,"text":"اضرب الفلسطينيون، ورفعوا الأعلام السود،"},{"id":"272","start":1683.42,"end":1686.22,"text":"وصفوا زيارة بلفور بالمشؤومة."},{"id":"273","start":1693.36,"end":1699.54,"text":"\"في الصورة المشهورة تجد سيدة من الخليل تضع ثلاثة حجابات على راسها،"},{"id":"274","start":1699.56,"end":1703.5,"text":"وسيدة في القدس تضع حجاب واحد ومعظم وجها ظاهر من تحت الحجاب،"},{"id":"275","start":1703.5,"end":1705.84,"text":"الوطنية المسيحية من دون حجاب، و"},{"id":"276","start":1706.24,"end":1710.32,"text":"مشين مع بعضهن وذهبن إلى بيت المندوب السامي و احتجّن واعترضن\"."},{"id":"277","start":1711.28,"end":1714.8,"text":"في المقابل، هنأ وايزمان صموئيل بما تحقق"},{"id":"278","start":1715.12,"end":1719.2,"text":"من انجاز، وكرّمت المنظمة الصهيونية صموئيل عام ١٩٢٥"},{"id":"279","start":1719.2,"end":1723.86,"text":"لإنهائه المرحلة الأولى من تأسيس الوطن القومي اليهودي."},{"id":"280","start":1725.24,"end":1729.52,"text":"كانت الحركة الصهيونية تعرض أفلاما دعائية في كل العالم،"},{"id":"281","start":1729.52,"end":1733.4,"text":"منها هذا الفيلم الموجه للناطقين بالفرنسية،"},{"id":"282","start":1733.4,"end":1735.58,"text":"يظهر إسم أرض إسرائيل"},{"id":"283","start":1735.58,"end":1737.46,"text":"على خارطة فلسطين،"},{"id":"284","start":1737.46,"end":1741.92,"text":"ويوضح مواقع الأراضي التي تدعي الصهيونية أنها حصلت عليها"},{"id":"285","start":1741.92,"end":1744.04,"text":"حتي عام ١٩٢٥"},{"id":"286","start":1744.26,"end":1747.86,"text":"وكانوا يدّعون انهم حصلوا على ١٧٧ ألف دونم."},{"id":"287","start":1748.34,"end":1752.36,"text":"كما ويظهر مخطط الصهيونية للحصول على الأراضي"},{"id":"288","start":1752.42,"end":1755.74,"text":"في الأعوام الخمسة والعشرون القادمة حيث ان المهمة للخمس والعشرون سنة القادمة هي الحصول على مليون دونم لصندوق الأراضي اليهودي."},{"id":"289","start":1761.74,"end":1770.94,"text":"في صيف عام ١٩٢٩ نظمت الوكالة اليهودية تجمعا صهيونيا للصلاة عند حائط البراق، للمطالبة بإعادة بناء الهيكل"},{"id":"290","start":1771.1,"end":1774.68,"text":"فقامت ثورة شعبية سميت بـ \"ثورة البراق\""},{"id":"291","start":1774.76,"end":1780.46,"text":"أطلقها فلاح فلسطيني من قرية المزار قضاء جنين وهو فرحان السعدي."},{"id":"292","start":1781.58,"end":1784.96,"text":"أصدر المندوب السامي البريطاني جون روبرت تشانسلور"},{"id":"293","start":1785.14,"end":1787.54,"text":"بيانا شديد اللهجة جاء فيه"},{"id":"294","start":1787.54,"end":1791.5,"text":"أنه سيوقع القصاص الصارم بمن شارك في الثورة،"},{"id":"295","start":1792.48,"end":1795.52,"text":"أعتقل السعدي مع العشرات، وكان منهم"},{"id":"296","start":1795.52,"end":1800.7,"text":"من صفد خريج الجامعة الأمريكية في بيروت فؤاد حسن حجازي،"},{"id":"297","start":1800.7,"end":1803.32,"text":"ومن الخليل عطا أحمد الزير،"},{"id":"298","start":1803.32,"end":1807.22,"text":"ومحمد خليل جمجوم، الذي لم نعثر على صوره له."},{"id":"299","start":1811.3,"end":1818.28,"text":"هنا في سجن القلعة في عكا سجن حجازي و الزير و جمجوم و حكم عليهم بالإعدام"},{"id":"300","start":1818.52,"end":1828.92,"text":"طالب الحكام العرب بتخفيف الحكم عنهم، لكن السلطات البريطانية في السابع عشر من يونيو لعام ثلاثين نفذت حكم الإعدام."},{"id":"301","start":1836.04,"end":1841.22,"text":"ما زالت قبور الشهداء الثلاثة في عكا، ونقرأ في وصيتهم:"},{"id":"302","start":1842.3,"end":1853,"text":"ولنا في آخر حياتنا رجاء إلى ملوك وأمراء العرب والمسلمين في أنحاء المعمورة، لا تثقون بالأجانب،"},{"id":"303","start":1853.18,"end":1856.86,"text":"وباسم العرب نحيا وباسم العرب نموت\"."},{"id":"304","start":1880.44,"end":1885.92,"text":"خلال عشر سنوات من الإنتداب، تضاعف عدد اليهود أكثر من ثلاث مرات"},{"id":"305","start":1886,"end":1889.38,"text":"ليصل إلى مائة وخمسة وسبعون ألفا"},{"id":"306","start":1890.02,"end":1893.12,"text":"افتخر الصهاينة في العالم بإنجازهم."},{"id":"307","start":1898.58,"end":1902.86,"text":"\"جئت اليوم لأسألكم أيها الزملاء الصهاينة، عن الموقف الذي سنتخذه من بين المواقف المتاحة أمامنا","voiceName":"ستيفن وايز – صهيوني امريكي في خطاب له في نيويورك سنه ١٩٣١"},{"id":"308","start":1903.38,"end":1906,"text":"أود أن أقول لإنجلترا، رغم أنني يهودي أمريكي،"},{"id":"309","start":1906.46,"end":1912.62,"text":"رغم أنني يهودي أمريكي، لكنني معجب منذ زمن ببريطانيا العظمى،"},{"id":"310","start":1912.9,"end":1915.5,"text":"سأقول لإنجلترا لو استطعت"},{"id":"311","start":1927.72,"end":1942.68,"text":"، إن وجود فلسطين عربية، هو تهديد لبريطانيا العظمى وخطر على العالم، ولكن وجود فلسطين يهودية، هو مكسب لبريطانيا العظمى وبركة للعالم\"."},{"id":"312","start":1960.9,"end":1964.06,"text":"كان ديفيد لويد جورج مع تصفيق وايزمن له"},{"id":"313","start":1964.06,"end":1967.5,"text":"يعمل لنقل ملكية فلسطين إلى الصهيوني."},{"id":"314","start":1970.98,"end":1974.54,"text":"\"كما ذكركم رئيسكم","voiceName":"ديفيد لويد جورج رئيس وزراء بريطانيا سابقا في حفل عشاء لصهيونيه في لندن سنه ١٩٣١"},{"id":"315","start":1976.16,"end":1978.94,"text":"فقد مرت ست عشرة سنة تقريبا،"},{"id":"316","start":1979.68,"end":1983.94,"text":"منذ أن جندني في الحركة الصهيونية،"},{"id":"317","start":1987.64,"end":2011.76,"text":"تحولت فلسطين من مستنقعات قاحلة موبوءة بالملاريا إلى مستعمرات مزدهرة، زرعت التربة بعد أن كانت المياه تضيع سدى منذ بدء الخليقة\"."},{"id":"318","start":2016.06,"end":2023.06,"text":"دون زرع و دون حياة، صحراء فارغة، تلك هي فلسطين"},{"id":"319","start":2023.06,"end":2029.14,"text":"هكذا كانت تُصور فلسطين في أوروبا"},{"id":"320","start":2029.44,"end":2034.42,"text":"وهكذا كانت فلسطين في الثلاثينيات."},{"id":"321","start":2046.2,"end":2050.6,"text":"\"اقسم بالله كان الزرع من طوله، لا تستطيع المشي خلاله، كان اطول مني،","voiceName":"حسني محمد صمادعة مواليد صبارين قضاء حيفا ويعيش في مخيم الجلزون للاجئين في رام الله"},{"id":"322","start":2048.78,"end":2063.26,"text":"الخضرة تملأ السهول، عندما تخرج في الصباح وتذهب الى البستان، تكون روائح الخضرة تفوح في كل مكان، ونوار الخيار والفقوس والبندورة يفوح عطرا،"},{"id":"323","start":2063.48,"end":2071.62,"text":"حيفا لا غنى عنها، اتذكرها جيدا، حيفا كانت ام الدنيا\"."},{"id":"324","start":2111.36,"end":2118.3,"text":"\"بالنسبة لي، طبريا هي أجمل مدينة وأجمل مكان في العالم."},{"id":"325","start":2118.4,"end":2124.14,"text":"المسيح لم يقم في مدينة طبريا،"},{"id":"326","start":2124.1,"end":2126.94,"text":"، وإنما أقام حول المدينة،"},{"id":"327","start":2127.2,"end":2133.84,"text":"فيها عمل معظم معجزاته، على مياهها مشى\"."},{"id":"328","start":2135.04,"end":2143.74,"text":"كان ديفيد لويد جورج وبلفور وتشرتشيل حريصين على تسمية الفلسطينيين بلقب \"المحمدين\"."},{"id":"329","start":2144.12,"end":2155.44,"text":"لم يعتبروا الفلسطينيين منتمين لمجموعة قومية عربية أو لمجموعات وطنية محلية،","voiceName":"الدكتور إيلان بابيه مؤرخ إسرائيلي جامعة اكسترا بريطانيا"},{"id":"330","start":2155.44,"end":2159.44,"text":"ولم يروا فيهم سوى مجموعة دينية،"},{"id":"331","start":2160.14,"end":2161.9,"text":"لقد تجاهلوا مثلا،"},{"id":"332","start":2162.24,"end":2168.06,"text":"أن المسيحيين والمسلمين في فلسطين كونوا هوية جديدة"},{"id":"333","start":2168.06,"end":2174.2,"text":"ليست هوية دينية، وإنما قومية\"."},{"id":"334","start":2174.8,"end":2186.12,"text":"\"قارة المسيحين الشرقيين غير موجودة، هنالك مأزق لدى الإستعمار عندما يستخدم الثيولوجيا المسيحية في رؤية المنطقة والشرق الأوسط،","voiceName":"عزمي بشارة مفكر عربي"},{"id":"335","start":2186.14,"end":2188.74,"text":"ما ادى إلى تبني الصهيونية فيما بعد"},{"id":"336","start":2189.06,"end":2201.58,"text":"مأزق التعامل مع المسيحين الشرقيين، هذا ليس موجودا في عالمه، لذلك يحاول الاستعمار أن ينكر وجودها أصلا ، لأنه مجرد وجودها يربك كل السبل\"."},{"id":"337","start":2208.38,"end":2217.28,"text":"عام ١٩٣٣ تصاعدت المظاهرات والاحتجاجات الشعبية في فلسطين، وشاركت النساء مع الرجال."},{"id":"338","start":2217.28,"end":2222,"text":"قمع البريطانيون المظاهرات وأعتقل الآلاف."},{"id":"339","start":2233.5,"end":2239.9,"text":"سقط كثير من الجرحى و الشهداء، حتي موسى كاظم الحسيني، رئيس بلدية القدس سابقا،"},{"id":"340","start":2240.08,"end":2247.52,"text":"ضربه الجنود البريطانيون وهو في الثمانين من العمر في مظاهرة بالقدس وتوفي متأثرا بجراحه."},{"id":"341","start":2264.4,"end":2267.1,"text":"كتب الشرطي الفلسطيني محسن توفيق،"},{"id":"342","start":2267.1,"end":2270.7,"text":"يحتج علي رئيسه الضابط البريطاني جورج فارادي،"},{"id":"343","start":2270.7,"end":2276.4,"text":"الذي أطلق خمسا وعشرون رصاصة بإتجاه الفلسطينيين المتظاهرين في يافا."},{"id":"344","start":2276.7,"end":2279.5,"text":"وجهت عدة احتجاجات ضد فارادي."},{"id":"345","start":2280,"end":2284.46,"text":"وكان رد الحكومة البريطانية بعد أربع سنوات من هذه الأحداث"},{"id":"346","start":2284.84,"end":2290.64,"text":"نح فارادي وسام الشرطة الملكي مع إشادة بدوره في فلسطين."},{"id":"347","start":2292.02,"end":2300.02,"text":"عدم رغبة بريطانيا بمنح المطالَب الوطنية للفلسطينيين، وعدم نظرتها للشعب الفلسطيني كمجموعة","voiceName":"الدكتور مصطفى كبها باحث في التاريخ الفلسطيني"},{"id":"348","start":2300.26,"end":2306.44,"text":"قومية وطنية ذات حقوق، ومعاملتهم على أساس أنهم طوائف،"},{"id":"349","start":2306.98,"end":2313.14,"text":"في حين معاملة الوكالة اليهودية على أساس أنها ممثلة لمجموعة قومية،"},{"id":"350","start":2313.4,"end":2319.52,"text":"كل هذا أدى إلى تفاقم الوضع والصدام مع بريطانيا\"."},{"id":"351","start":2321.6,"end":2325.36,"text":"لا يتوفر في الأرشيف غير صور ثابتة لبعض المظاهرات الفلسطينية،"},{"id":"352","start":2325.36,"end":2327.36,"text":"وتنعدم الصور المتحركة،"},{"id":"353","start":2327.52,"end":2334.7,"text":"ينما صورت كاميرات السينما كافة الأنشطة الصهيونية والبريطانية."},{"id":"354","start":2338.64,"end":2342.62,"text":"تل أبيب ترفع أواسط الثلاثينيات العلم الصهيوني."},{"id":"355","start":2343.08,"end":2348.04,"text":"وتصدير القطن والألماس من المصانع اليهودية"},{"id":"356","start":2349.18,"end":2353.18,"text":"، والهجرة اليهودية وبناء المستوطنات."},{"id":"357","start":2358.4,"end":2365.86,"text":"كان من أهم تحديات هذا العمل أن نجد الصور الأرشيفية التي حاولت الدعاية الصهيونية محوها.","voiceName":"الصحفية روان الضامن"},{"id":"358","start":2366.1,"end":2371.56,"text":"استغرق الأمر أشهرا من البحث في الأرشيف البريطاني، هنا في لندن،"},{"id":"359","start":2371.8,"end":2376.76,"text":"لنجد بالصوت والصورة بعضا من الحقيقة المغيبة."},{"id":"360","start":2439.14,"end":2444.82,"text":"معظم الفلسطينيين مثقفين وقادة وصحفيين،","voiceName":"الدكتور إيلان بابيه مؤرخ إسرائيلي جامعة اكسترا بريطانيا"},{"id":"361","start":2444.84,"end":2446.82,"text":"كانوا ما يزالون غير واعين"},{"id":"362","start":2446.82,"end":2451.4,"text":"لمدى تصميم الحركة الصهيونية على إخراجهم من فلسطين\"."},{"id":"363","start":2451.34,"end":2456.24,"text":"\"تكشف الأرقام عن تزايد أعداد المهاجرين اليهود إلى فلسطين"},{"id":"364","start":2456.28,"end":2462.24,"text":"، من أربعة آلاف عام ١٩٣١، إلى تسعة آلاف وخمسمائة في العام التالي،"},{"id":"365","start":2462.32,"end":2465.76,"text":"ثم إلى ثلاثين ألفا عام ١٩٣٣"},{"id":"366","start":2465.74,"end":2468.78,"text":"وأربعة وثلاثين عام ١٩٣٤،"},{"id":"367","start":2468.92,"end":2478.46,"text":"حتى وصل ذروته إلى اثنين وستين ألفا عام ١٩٣٥. في نفس العام ألقي الشاعر الفلسطيني عبد الرحيم محمود"},{"id":"368","start":2478.58,"end":2484.4,"text":"قصيدة أمام الأمير سعود بن عبد العزيز عندما جاء لزيارة القدس، قال فيها:"},{"id":"369","start":2484.82,"end":2486.72,"text":"المسجد الأقصى،"},{"id":"370","start":2486.88,"end":2488.72,"text":"أجئت تزوره،"},{"id":"371","start":2489.1,"end":2492.62,"text":"أم جئت من قبل الضياع، تودعه\"."},{"id":"372","start":2493.42,"end":2494.84,"text":"\"هذه الأراضي تصادر،","voiceName":"الدكتور مهدي عبد الهادي رئيس الجمعية الفلسطينية الأكاديمية للشؤون الدولية"},{"id":"373","start":2495.28,"end":2496.84,"text":"الوعد يتم تنفيذه،"},{"id":"374","start":2497.12,"end":2505.24,"text":"وغياب إي تفاعل معها من الحكم البريطاني، كان هناك القمع والبطش وقوانين رهيبة في الحكم البريطاني، وحتى يومنا هذا، إسرائيل تطبقها"},{"id":"375","start":2505.84,"end":2508.38,"text":"بالاعتقالات والإبعاد والبطش،"},{"id":"376","start":2508.38,"end":2510.68,"text":"فتحرك سنه ١٩٣٥ عز الدين القسام\"."},{"id":"377","start":2510.82,"end":2513.68,"text":"لشهيد عز الدين القسام يمثل حالة في الثلاثينيا","voiceName":"ماجد الزير مدير عام مركز العودة الفلسطيني"},{"id":"378","start":2513.96,"end":2519.42,"text":"، حالة في بعدها العربي للقضية، بأنه جاء من سوريا،"},{"id":"379","start":2519.4,"end":2523.1,"text":"و في البعد الديني للقضية، وارتباطه بالمسجد،"},{"id":"380","start":2523.1,"end":2527.92,"text":"بتنظيمه وعناده في المقاومة وفي إصراره على مقاومة المحتل\"."},{"id":"381","start":2527.92,"end":2537.12,"text":"عندما أرسل القسام للقيادة الفلسطينية في القدس، ليستشيرهم في الثورة، قالوا أن الوقت غير مناسب لإعلان ثورة،","voiceName":"يوسف حجازي صحفي وباحث من غزة"},{"id":"382","start":2537.22,"end":2547.68,"text":"والشعب غير ناضج لإعلان الثورة، وقالوا أيضا، نحن ما زلنا نراهن على إمكانية الحصول على حقوقنا من خلال المفاوضات السياسية\"."},{"id":"383","start":2548.82,"end":2556.94,"text":"وحال إعلان الشيخ عز الدين القسام الثورة المسلحة، حاصره الجيش البريطاني في كمين في أحراج يعبد قرب جنين،"},{"id":"384","start":2556.94,"end":2564.96,"text":"وقصفوا مجموعته بالطائرات والمدافع، وأستشهد مع رفيقيه يوسف الزيباوي وسيد حنفي عطية."},{"id":"385","start":2565.38,"end":2570.54,"text":"\"حركة القسام لم تحظى بالاهتمام الكبير من جانب المؤرخين،","voiceName":"الدكتور مصطفى كبها باحث في التاريخ الفلسطيني"},{"id":"386","start":2570.64,"end":2575.36,"text":"لا نعرف عنها الكثير بسبب الغموض الذي اكتنفها، وبسبب"},{"id":"387","start":2575.46,"end":2579.34,"text":"الحرص الشديد للشيخ عز الدين القسام على السرية في العمل،"},{"id":"388","start":2579.34,"end":2587.26,"text":"وعدم إطلاع الكثيرين، إلا الصفوة الصغيرة من طلابه\"."},{"id":"389","start":2587.4,"end":2593.02,"text":"\"امتدت روح عز الدين القسام في الوسط الفلسطيني،","voiceName":"جودت الحاج محمد – ابن الشهيد عبد الرحيم الحاج محمد"},{"id":"390","start":2593.2,"end":2596.42,"text":"تأثر بها كثير من الفلسطينيين،"},{"id":"391","start":2596.92,"end":2607.68,"text":"فبدأت مشاعر الانقلاب أو التمرد على الإستعمار البريطاني تظهر في صفوف الشعب الفلسطيني\"."},{"id":"392","start":2608.92,"end":2614.34,"text":"وتعرضت القيادة السياسية الفلسطينية إلى ضغوط لوقف الحوار مع الإنجليز"},{"id":"393","start":2615.18,"end":2621.56,"text":"حتى أن الشاعر الفلسطيني إبراهيم طوقان كتب متوجها للقيادة عام ١٩٣٥:"},{"id":"394","start":2622.06,"end":2624.86,"text":"\"أنتم المخلصون للوطنية،"},{"id":"395","start":2624.86,"end":2627.78,"text":"أنتم الحاملون عبئ القضية،"},{"id":"396","start":2627.78,"end":2630.96,"text":"في يدينا بقية من بلاد،"},{"id":"397","start":2630.96,"end":2634.34,"text":"فاستريحوا كي لا تطير البقية\"."},{"id":"398","start":2635.82,"end":2637.68,"text":"\"فبدأ الإضراب في يافا","voiceName":"يوسف حجازي صحفي و باحث من غزة"},{"id":"399","start":2638.14,"end":2641.5,"text":"بحرية يافا ١٩ نيسان ١٩٣٦."},{"id":"400","start":2641.78,"end":2649.9,"text":"القيادة والأحزاب الفلسطينية، نحن قلنا ان القيادة، الحركة الوطنية الفلسطينية، كانت منقسمة إلى أحزاب تقوم على اساس التناحر العائلي."},{"id":"401","start":2650.02,"end":2661.72,"text":"، الأحزاب الفلسطينية الستة شكلت قيادة - اللجنة العربية العليا ٢٥- ٤ - ١٩٣٦ بعد ستة أيام من الثورة حتى تقود الثورة، وقادت الثورة فعلا."},{"id":"402","start":2661.74,"end":2668.24,"text":"إن الشكوى الرئيسية لدى العرب، هي ضد سياسة الحكومة البريطانية في فلسطين، فهي سياسة إذا ما استمرت","voiceName":"المتحدث باسم اللجنة العربية العليا 1936"},{"id":"403","start":2668.24,"end":2677.1,"text":"فستؤدي حتما إلى إحلال اليهود محل العرب،"},{"id":"404","start":2677.14,"end":2686.9,"text":"وخلافا لكل المبادئ، فقد فرضت الحكومة البريطانية تصريح بلفور، الذي يمقته جميع العرب في الشرق الأدنى،"},{"id":"405","start":2687.06,"end":2698.94,"text":"وبتفضيلها تأسيس وطن قومي لليهود، تناست بقصد، حماية الحقوق المدنية للسكان غير اليهود."},{"id":"406","start":2698.96,"end":2703.9,"text":"لقد قرر العرب القيام بإضراب عام"},{"id":"407","start":2703.94,"end":2709.3,"text":"و شامل حتي تتوقف هجرة اليهود بشكل كامل،"},{"id":"408","start":2709.36,"end":2715.08,"text":"وحتي تجري الحكومة البريطانية تغييرا جوهريا في سياستها الحالية\"."},{"id":"409","start":2715.6,"end":2720.24,"text":"\"لأنه حدث الإضراب، الذي هو من أعظم الإضرابات في التاريخ","voiceName":"للصحفي مسلم بسيسو مراسل وكالة الأسوشيتد برس في فلسطين ٤٨"},{"id":"410","start":2720.46,"end":2724.62,"text":"في سنه ١٩٣٦ كل فلسطين أضربت"},{"id":"411","start":2725.98,"end":2732.44,"text":"، وهذا كان نتيجة النضالات التي حدثت في مواجهة الإنتداب"},{"id":"412","start":2733.14,"end":2744.28,"text":"وحكم الإنتداب، و تحيز الإنتداب مع اليهود، تسهيل الأمور لهم في كل شيئ\"."},{"id":"413","start":2745.68,"end":2748.48,"text":"كان إضرابا شاملا، هز الإنتداب البريطاني،"},{"id":"414","start":2748.44,"end":2751.12,"text":"فاتخذت السلطات البريطانية إجراءات عقابية صارمة"},{"id":"415","start":2751.12,"end":2754.26,"text":"اعتقال كل من يشتبه بعلاقته بالثوار"},{"id":"416","start":2754.4,"end":2763.14,"text":"وتفجير منازلهم. هدم أكثر من مئتي منزل في يافا وحدها كتنكيل جماعي، وتلتها مدن وقرى أخرى."},{"id":"417","start":2763.46,"end":2770.82,"text":"وأصر البريطانيون على أن هدم المنازل عمل مبرر للقضاء على الثورة."},{"id":"418","start":2774.96,"end":2778.08,"text":"ماذا تتذكر عن الإنجليز في البلد ماذا كانوا يعملون؟","voiceName":"الصحفية روان الضامن"},{"id":"419","start":2781.44,"end":2792.04,"text":"الإنجليز، الإنجليز اقاموا مركزهم على الطريق،","voiceName":"سامي كمال عبد الرزاق مواليد ترشيحة قضاء عكا"},{"id":"420","start":2792.36,"end":2798.04,"text":"وكان خالي مع عماله في طريقهم الى العمل،"},{"id":"421","start":2798.62,"end":2808.16,"text":"فقالوا له أدخل أنت وشغيلتك لتكنس المركز، فرفض، فضربوه، ضربه"},{"id":"422","start":2809.4,"end":2813.7,"text":"ضربه ضابط اسمه ريمون،"},{"id":"423","start":2813.84,"end":2827.72,"text":"واستمروا في ضربه حتى مات، آخذناه الى مستشفى حمزة في حيفا، لتجرى له عملية جراحية، فوجدنا منديل الشحم في صدره قد تمزق من ضدة الضرب فمات\"."},{"id":"424","start":2828.46,"end":2830.58,"text":"\"كانوا يضربوننا\"","voiceName":"حسني محمد صمادعة ,مواليد صبارين قضاء حيفا, يعيش في مخيم الجلزون قرب مدينة رام الله"},{"id":"425","start":2830.58,"end":2831.74,"text":"ماذا يعملون؟"},{"id":"426","start":2831.74,"end":2841.32,"text":"يضربون الرجال ويأخذوهم الى السجن في طولكرم، والى بلدة جرجور بالقرب من بن يامين،"},{"id":"427","start":2841.84,"end":2850.94,"text":"ويجبرونهم على العمل في المحاجر، ويأتون بهم على قرية نور شمس للعمل في المحاجر، اي أشغال شاقة\"."},{"id":"428","start":2851.1,"end":2853.7,"text":"لو تقرأين مذكرات بن غوريون،","voiceName":"الدكتور مهدي عبد الهادي رئيس الجمعية الفلسطينية الأكاديمية للشؤون الدولية"},{"id":"429","start":2853.7,"end":2859.24,"text":"كان يتحدى، يتحدى المفاوض العربي في قمة الثورة، خلال قمة ١٩٣٦"},{"id":"430","start":2859.46,"end":2861.42,"text":"في جلساته مع موسى العلمي،"},{"id":"431","start":2861.42,"end":2864.16,"text":"التي رتب لها واك هوب المندوب السامي،"},{"id":"432","start":2864.18,"end":2873.04,"text":"قال له: \"نحن لنا حضور فعلي، لا يستطيع الغرب، في بريطانيا أن يقول لنا لا، من أنتم في بريطانيا؟\"."},{"id":"433","start":2873.32,"end":2880.18,"text":"أثناء الإضراب بحث ديفيد بن غوريون، رئيس الوكالة اليهودية مع المندوب السامي البريطاني آرثر واك هوب،"},{"id":"434","start":2880.32,"end":2886.84,"text":"إمكانية توطين الفلاحين الفلسطينيين، الذين يتم طردهم من أراضي المستعمرات اليهودية"},{"id":"435","start":2886.84,"end":2888.4,"text":"في شرق الأردن،"},{"id":"436","start":2888.4,"end":2889.8,"text":"فأجابه:"},{"id":"437","start":2889.84,"end":2891.8,"text":"إنها فكرة جيدة."},{"id":"438","start":2892,"end":2896,"text":"نهاية الجزء الاول."}]},"id":"001"}]}