// autogenerated from /content/content.ru.md
    // via /src/utils/createContent.js
    export default {"info":{"title":"Трагедия Накба \\n часть первая: нити заговора","paramsArray":[["Издатель: Аль-Джазира"]]},"content":[{"title":"1","type":"richMedia","params":["hiddenContent/001","s0-344"],"content":"","id":"001"},{"title":"2","type":"richMedia","params":["hiddenContent/001","s344-615"],"content":"","id":"002"},{"title":"3","type":"richMedia","params":["hiddenContent/001","s615-905"],"content":"","id":"003"},{"title":"4","type":"richMedia","params":["hiddenContent/001","s905-1261"],"content":"","id":"004"},{"title":"5","type":"richMedia","params":["hiddenContent/001","s1261-1531"],"content":"","id":"005"},{"title":"6","type":"richMedia","params":["hiddenContent/001","s1531-1835"],"content":"","id":"006"},{"title":"7","type":"richMedia","params":["hiddenContent/001","s1835-2109"],"content":"","id":"007"},{"title":"8","type":"richMedia","params":["hiddenContent/001","s2109-2394"],"content":"","id":"008"},{"title":"9","type":"richMedia","params":["hiddenContent/001","s2394-2715"],"content":"","id":"009"},{"title":"10","type":"richMedia","params":["hiddenContent/001","s2715-2940"],"content":"","id":"010"}],"hiddenContent":[{"title":"Nakbah-1-movie","type":"richMedia","params":["https://www.youtube.com/watch?v=fbcZhO-QJQU"],"content":{"phrases":[{"id":"000","start":0,"end":0,"text":""},{"id":"001","text":"Трагедия Накба"},{"id":"002","text":"Часть первая. Нити заговора."},{"id":"003","text":"“Трагедия Палестины не локальная, а всемирная, потому что несправедливость разрушает мир во всём мире”.","voiceName":"Арнольд Тойнби, британский всемирный историк"},{"id":"004","text":"город Акка"},{"id":"005","text":"Накба началась не 60 лет назад, в 1948 году."},{"id":"006","text":"Она началась в политике 200 лет назад."},{"id":"007","text":"В 1799 году эти стены в Акке противостояли Наполеону,"},{"id":"008","text":"который направил свои колониальные мысли против расширения Британии,"},{"id":"009","text":"и обратился к евреям всего мира:"},{"id":"010","text":"“Израильтяне,"},{"id":"011","text":"вставайте! Вот подходящий момент!"},{"id":"012","text":"Франция протягивает вам свою руку, чтобы помочь нести наследие Израиля!"},{"id":"013","text":"Спешите вернуть свое положение среди народов мира!”"},{"id":"014","text":"Воззвание Наполеона превратилось в главную новость французских газет."},{"id":"015","text":"Наполеон был разгромлен, и в Акке, в память о нем осталась лишь табличка с его изображением,"},{"id":"016","text":"на холме, носящем его имя."},{"id":"017","text":"Однако, идея присутствия евреев в регионе не умерла."},{"id":"018","text":"Спустя 40 лет Британия вернулась к этой идее"},{"id":"019","text":"в ответ на попытку Мухаммада Али объединить Сирию и Египет."},{"id":"020","text":"В 1840 году министр иностранных дел Великобритании Генри Пальмерстон написал своему послу в Стамбуле:"},{"id":"021","text":"“Ты должен убедить султана и его окружение,"},{"id":"022","text":"что пришло время открыть Палестину для еврейских переселенцев”."},{"id":"023","text":"На то время численность евреев в Палестине не превышала 3 тысяч человек."},{"id":"024","text":"Евреи откликнулись на инициативу Британии. Барон Эдмон де Ротшильд был из первых,"},{"id":"025","text":"посетивших Палестину. Он побывал в ней четыре раза, выжидая удачные моменты для инвестиций."},{"id":"026","text":"И вложил более 14 млн франков в основание 30 еврейских колоний."},{"id":"027","text":"Одна из ключевых колоний, Ришон-Ле-Цион,"},{"id":"028","text":"подняла над ней этот особенный флаг,"},{"id":"029","text":"в 1885 году, в то время как Палестина была под властью Османской Империи."},{"id":"030","text":"Останки Барона Ротшильда находятся в одном из храмов возле Хайфы и израильские дети посещают их,"},{"id":"031","text":"чтобы узнать о богатстве, поддержавшем Израиль более 100 лет назад."},{"id":"032","text":"В 1885 году впервые появился термин “сионисткое движение”,"},{"id":"033","text":"из под руки австрийского писателя Натана Бирбаума."},{"id":"034","text":"Целью движения была колонизация Палестины."},{"id":"035","text":"Термин произошел от слова Сион, названия одного из холмов Иерусалима."},{"id":"036","text":"“Конечно же, в Османском государстве была диаспора евреев.","voiceName":"Доктор Хилел Коэн, историк и исследователь в Еврейском университете"},{"id":"037","text":"Но они не были сионистами. Они были местными евреями."},{"id":"038","text":"Но прибывшие в конце 19 века из Европы, и особенно из Восточной,"},{"id":"039","text":"хотели построить новое еврейство”."},{"id":"040","text":"В 1896 году сионистский журналист Теодор Герцль опубликовал свою книгу “Еврейское государство”. Она вышла в свет на немецком языке."},{"id":"041","text":"Однако евреи ожидали своей заветной мечты переселиться в Америку."},{"id":"042","text":"Тогда Макс Нордау, врач, социолог и правая рука Теодора Герцля, направил двух крупнейших иудейских богословов в Палестину."},{"id":"043","text":"Они прислали ответ:"},{"id":"044","text":"“Невеста очень красивая и отвечает всем требованиям, но замужем”."},{"id":"045","text":"Нордау понял, что Палестина не такая безлюдная как рассказывал Герцль,"},{"id":"046","text":"а наоборот, заселена народом, который живёт там тысячи лет."},{"id":"047","text":"Летом следующего года Нордау и Бирнбаум под руководством Герцля участвовали в первом сионистском конгрессе в Базеле, Швейцария."},{"id":"048","text":"Там была принята программа создания общепризнанной родины еврейского народа в Палестине."},{"id":"049","text":"“Герцль начал официально призывать к созданию государства.","voiceName":"Доктор Анис Саиг, главный редактор “Энциклопедии Палестины”"},{"id":"050","text":"Он поддерживал связи с могущественными странами, особенно в Европе, чтобы защитить его."},{"id":"051","text":"Герцль, без сомнения, отец сионистского государства. Но сама идея сионизма принадлежит не ему”."},{"id":"052","text":"“Обычно между крупными странами бывает соперничество и конфликт интересов.","voiceName":"Журналистка Рован Домен"},{"id":"053","text":"Как на этот раз четыре крупные державы смогли вместе поддержать идею сионизма?”."},{"id":"054","text":"“Да, не было совместной конференции этих стран, но тем не менее они сошлись на том, чтобы поддержать идею.","voiceName":"Доктор Анис Саиг, главный редактор “Энциклопедии Палестины”"},{"id":"055","text":"Сионисты возбудили соперничество между странами, каждой ложно пообещав:"},{"id":"056","text":"\\\"Мы будем с вами, если вы поможете Израилю против других стран,"},{"id":"057","text":"мы будем защищать ваши интересы предже интересов других стран\\\"."},{"id":"058","text":"Англия подтвердила свою заинтересованность в основании еврейского государства."},{"id":"059","text":"Премьер-министр Кемпбелл-Баннерман заявил"},{"id":"060","text":"о важности создания мощной и невиданной доселе защиты для моста, связывающего Средиземное и Красное моря. Он сказал:"},{"id":"061","text":"“Мы должны поместить в этом регионе и окрестностях Суэцкого канала режимы, враждебные местным народам и дружественные европейским странам”."},{"id":"062","text":"“Плоть этого государства упирается в западный колониализм и европейцев.","voiceName":"Доктор Мухсин Солих, директор исследовательского центра “Зайтуна”"},{"id":"063","text":"Гарантия его существования - слабость всех окружающих стран\\\"."},{"id":"064","text":"“Европейцы считают, что евреи ближе им, чем арабы, поэтому создание еврейского государства будет благом для них”.","voiceName":"Доктор Хилел Коэн, историк и исследователь в Еврейском университете"},{"id":"065","text":"В 1907 году британский химик и член Всемирного сионистского движения Хаим Вейцман впервые направился в Палестину,"},{"id":"066","text":"чтобы при поддержке семьи Ротшильдов основать в Яффе компанию “Развитие палестинских земель”."},{"id":"067","text":"Целью была систематическая скупка палестинских земель."},{"id":"068","text":"Три года спустя свершилась важная сделка."},{"id":"069","text":"Еврейский национальный фонд приобрел более 200 тыс. акров равнины Мардж Ибн Амир на севере Палестины."},{"id":"070","text":"Продавцом была ливанская семья Сурсук, проживавшая в Европе. Они скупили эти земли заранее у османских офицеров."},{"id":"071","text":"Сделка предусматривала выселение тысяч крестьян, проживавших там."},{"id":"072","text":"“Более 60 тыс. палестинцев было выселено с долины Мардж Ибн Амир.","voiceName":"Адвокат Ваким Ваким, секретарь общества по защите прав переселенцев"},{"id":"073","text":"Если под Накбой мы подразумеваем выселение палестинцев с их земель"},{"id":"074","text":"и захват её силой,"},{"id":"075","text":"то в обоих этих смыслах Накба началась на десятки лет раньше 1948 года”."},{"id":"076","text":"“Это было хуже, чем известная европейская колонизация.","voiceName":"Журналист Дэвид Хирст, специализирующийся на ближневосточной тематике"},{"id":"077","text":"Потому что людей не эксплуатировали в их стране, а изгоняли их оттуда”."},{"id":"078","text":"“С самого первого момента, когда сионистское движение выбрало Палестину территорией для независимого еврейского государства,","voiceName":"Доктор Илан Паппе, израильский историк, университет Экстер, Британия"},{"id":"079","text":"было ясно, что эта земля заселена палестинцами."},{"id":"080","text":"И сионисты были убеждены, что есть только один способ сделать Палестину еврейской страной - выселить из нее палестинцев”."},{"id":"081","text":"“Выселение крестьян состоит из двух частей. Первая, овладение землей, называемая евреизация земли.","voiceName":"Доктор Азми Бишара, арабский мыслитель"},{"id":"082","text":"Вторая, называемая еврейской работой, это замена арабских крестьян на"},{"id":"083","text":"на еврейских, из Восточной Европы, а в некоторых случаях, когда их не хватало - евреями из Йемена”."},{"id":"084","text":"К тому времени была сформирована еврейская гвардия Ха-Шомер для защиты еврейских поселений."},{"id":"085","text":"Также евреи поставили вопрос о признании иврита османскими властями."},{"id":"086","text":"“Любой араб и палестинец с первого дня осознавали концепцию сионизма.","voiceName":"Доктор Махди Абдуль-Хади, глава Палестинского Академического общества по международным делам"},{"id":"087","text":"Это расистское движение, ищущее капитал для колонизации земли и инвестирования в религиозный фактор,"},{"id":"088","text":"чтобы сделать эту землю родиной для евреев всего мира, создать идентичность и политическую перспективу."},{"id":"089","text":"Это было отчетливо ясно Наджибу Азури и Наджибу Нассару”."},{"id":"090","text":"Фармацевт Наджиб Нассар стал выпускать в Хайфе газету “Кармель”,"},{"id":"091","text":"чтобы предостеречь от амбиций сионистов."},{"id":"092","text":"Он утверждал, что государство евреев станет отравленным кинжалом в боку арабов."},{"id":"093","text":"“Кармель” была маленькой газетой, где работало всего два человека, сам Наджиб Нассар и его жена Сазидж Бахаи."},{"id":"094","text":"Несмотря на это Нассар не избежал репрессий со стороны турецких властей и заключения."},{"id":"095","text":"1914 Первая мировая война."},{"id":"096","text":"“Во время войны британцы думали о положении Палестины.","voiceName":"Доктор Юджин Роган, глава центра “Ближний Восток”, Британия"},{"id":"097","text":"Она близка к Суэцкому каналу и роль Британии в Египте на то время"},{"id":"098","text":"подталкивала её играть особую роль и в Палестине."},{"id":"099","text":"И британцы увидели в евреях и сионистском движении стратегического партнера для колонизации”."},{"id":"100","text":"В 1915 году на заседании совета министров Британии был представлен секретный меморандум: “Будущее Палестины”."},{"id":"101","text":"Его написал первый еврей-сионист достигший должности британского министра Герберт Самуэль."},{"id":"102","text":"В документе были такие строки:"},{"id":"103","text":"“Настоящее время не подходит для создания независимого еврейского государства."},{"id":"104","text":"Поэтому, после войны следует поместить Палестину под британское покровительство."},{"id":"105","text":"Это даст возможности для еврейских организаций скупать земли, основывать колонии и организовать миграцию."},{"id":"106","text":"Мы должны посеять среди магометан 3-4 миллиона европейских евреев”."},{"id":"107","text":"Предложения Самуэля вылились в принятие секретного соглашения между Британией и Францией по разделу Великой Сирии."},{"id":"108","text":"Этот документ известен под именем двух его авторов"},{"id":"109","text":"британца Марка Сайкса и француза Жоржа Пико."},{"id":"110","text":"Соглашение Сайкса-Пико отдаёт Палестину под совместное управление союзников"},{"id":"111","text":"для её подготовки к созданию еврейского государства."},{"id":"112","text":"“Мы из Палестины, а Палестина - часть Шама (Леванта), у нас один язык…","voiceName":"Умар Натур, уроженец Язура, провинция Яффа, живущий в лагере Балата возле города Наблус"},{"id":"113","text":"Сайкс и Пико"},{"id":"114","text":"сделали так, что нам опротивел английский и разговоры о них."},{"id":"115","text":"Но мы должны помнить о том, кто раздробил нашу родину, что бы заселить Палестину евреями”."},{"id":"116","text":"Марк Сайкс был близок с Хаимом Вейцманом"},{"id":"117","text":"и они обменивались письмами перед подписанием соглашения,"},{"id":"118","text":"в которых Сайкс выражал поддержку сионистскому движению."},{"id":"119","text":"Через год совет министров Британии под руководством Дэвида Ллойда Джорджа"},{"id":"120","text":"согласился дать обещание о создании еврейского национального очага в Палестине."},{"id":"121","text":"Они оформили его в виде письма"},{"id":"122","text":"от министра иностранных дел Артура Бальфура"},{"id":"123","text":"к сионистскому лорду"},{"id":"124","text":"Лайонелу Уолтеру Ротшильду."},{"id":"125","text":"Бальфур представлял Британское правительство,","voiceName":"Доктор Юджин Роган, глава центра “Ближний Восток”, Британия"},{"id":"126","text":"когда писал письмо лорду Ротшильду."},{"id":"127","text":"он писал от имени"},{"id":"128","text":"Британского государства"},{"id":"129","text":"А Ротшильд был лоббистом,"},{"id":"130","text":"подталкивающим Британию"},{"id":"131","text":"сыграть её роль"},{"id":"132","text":"в заселении Палестины евреями."},{"id":"133","text":"Он представлял сионистское движение."},{"id":"134","text":"За всеми ними стоял Хаим Вейцман,"},{"id":"135","text":"отвечавший за создание сионистского государства"},{"id":"136","text":"отвечавший за создание сионистского государства и участвовавший в составлении обещания”."},{"id":"137","text":"У Британии не было ни морального, ни политического, ни юридического права","voiceName":"Доктор Ави Шлаим, израильский историк, Оксфордский университет, Британия"},{"id":"138","text":"подготавливать землю, находящуюся в собственности арабов, для передачи другим людям."},{"id":"139","text":"Обещание Бальфура аморально и незаконно”"},{"id":"140","text":"Спустя месяц после обещания Бальфура"},{"id":"141","text":"в Лондоне праздновали успех сионистов."},{"id":"142","text":"На мероприятии выступили лорд Ротшильд,"},{"id":"143","text":"Герберт Самуэль, Марк Сайкс и Хаим Вейцман."},{"id":"144","text":"Спустя несколько дней британская армия под командованием генерала Эдмунда Алленби аккупировали"},{"id":"145","text":"Иерусалим"},{"id":"146","text":"Вместе с войсками Алленби вошёл и еврейский армейский корпус,"},{"id":"147","text":"подготовленный и руководимый министром британских колоний"},{"id":"148","text":"Уинстоном Черчиллем."},{"id":"149","text":"Среди членов этого корпуса был"},{"id":"150","text":"Давид Бен Гурион,"},{"id":"151","text":"Зеев Жаботинский"},{"id":"152","text":"и Эдвин Самуэль, старший сын Герберта Самуэля"},{"id":"153","text":"Нехемия Рабин,"},{"id":"154","text":"отец Ицхака Рабина."},{"id":"155","text":"В следующем месяце Алленби и его близкий друг Хаим Вейцман в Иерусалиме."},{"id":"156","text":"Численность евреев в Палестине была 50 тыс, против 500 тыс. арабов,"},{"id":"157","text":"то есть около 10% от всего населения."},{"id":"158","text":"После окончания Первой мировой войны и во время подготовки к Парижской мирной конференции,"},{"id":"159","text":"президент США Вильсон отправил"},{"id":"160","text":"на Ближний Восток комитет во главе с академиком, доктором Генри Кингом"},{"id":"161","text":"и политиком Чарльзом Крейном,"},{"id":"162","text":"чтобы они изучили ситуацию."},{"id":"163","text":"В докладе, который они подготовили, были следующие выводы:"},{"id":"164","text":"Если мы хотим применить принципы американского правосудия,"},{"id":"165","text":"то следует учесть желания палестинского народа,"},{"id":"166","text":"и предоставить им право выбирать будущее Палестины."},{"id":"167","text":"А 90% её населения против сионистской программы."},{"id":"168","text":"Такие же настроения и во всей Сирии."},{"id":"169","text":"Каждый британский чиновник, с кем мы встречались, подтвердил,"},{"id":"170","text":"что сионистскую программу можно насадить только силой оружия,"},{"id":"171","text":"для чего понадобится по меньшей мере 50 тыс. военных."},{"id":"172","text":"Уже это само по себе говорит о несправедливости сионистской программы."},{"id":"173","text":"Опираясь на вышесказанное, мы рекомендуем отказаться от проекта создания в Палестине еврейского государства”."},{"id":"174","text":"Доклад Кинга и Крейна не был услышан."},{"id":"175","text":"На парижской мирной конференции от Британии участвовали Дэвид Ллойд Джордж и Артур Бальфур."},{"id":"176","text":"Сионистскую делегацию возглавил Хаим Вейцман,"},{"id":"177","text":"чтобы продемонстрировать карту с обозначенной территорией еврейской страны."},{"id":"178","text":"Она включала Палестину от долины Иордана на востоке и от Сайды в Ливане"},{"id":"179","text":"на юге и Аль-Кунейтры в Сирии"},{"id":"180","text":"и шла параллельно Хиджазской железной дороге."},{"id":"181","text":"На конференции Алленби, Дэвид Ллойд Джордж"},{"id":"182","text":"заключили договоренность с эмиром Фейсалом ибн Хусейном,"},{"id":"183","text":"известную как “соглашение Вейцмана-Фейсала”."},{"id":"184","text":"Однако настоящим автором соглашения был британский полковник"},{"id":"185","text":"Томас Эдвард Лоуренс, известный как Лоуренс Аравийский."},{"id":"186","text":"Фейсал подписал его, но дописал своей рукой, что договоренность будет в силе только если"},{"id":"187","text":"арабам предоставят независимость."},{"id":"188","text":"Сионистское движение примерно с 1919 осознало,","voiceName":"Доктор Хилел Коэн, историк и исследователь в Еврейском университете"},{"id":"189","text":"что нельзя надеяться только на внешнюю политику."},{"id":"190","text":"Да, у нас есть связи в Лондоне, офис в Нью-Йорке, Вашингтоне и друзья в Берлине…."},{"id":"191","text":"Тот, кто находится здесь, должен принять во внимание местных людей."},{"id":"192","text":"Поэтому в 1919 году был основан разведывательный центр"},{"id":"193","text":"сионистского движения."},{"id":"194","text":"Были налажены хорошие связи между арабами и евреями во многих областях."},{"id":"195","text":"Здесь были евреи, родители которых иммигрировали сюда в 1884, они родились и выросли в Палестине, говорили на арабском, ездили верхом на лошадях,"},{"id":"196","text":"находились среди арабов и дружили с ними."},{"id":"197","text":"Они стали собирать информацию политического характера."},{"id":"198","text":"Каково общественное мнение?"},{"id":"199","text":"Согласны ли люди с сионистским проектом или нет?"},{"id":"200","text":"Есть ли незаселенные земли? Есть ли желающие продать свои земли евреям?"},{"id":"201","text":"Планирует ли кто-нибудь нападения на евреев из определенной местности или сообщества."},{"id":"202","text":"В 1920 году был назначен первый британский губернатор для Палестины."},{"id":"203","text":"И выбор правительства Британии на должность верховного комиссара"},{"id":"204","text":"пал на еврея сиониста Герберта Самуэля."},{"id":"205","text":"Самуэль пришёл реализовать то, что он предлагал 5 лет назад -"},{"id":"206","text":"подготовить Палестину к созданию еврейского государства."},{"id":"207","text":"Самуэль и Алленби были друзьями и их переписка указывает на полное взаимопонимание относительно роли Британии в этом деле."},{"id":"208","text":"Вторая статья документа британского мандата, одобренного Лигой наций гласит:"},{"id":"209","text":"Британии поручается привести страну в соответствующее положение: политическое, административное и экономическое,"},{"id":"210","text":"которое бы позволило создать еврейское национальное государство”."},{"id":"211","text":"С самого первого дня Самуэль рассматривал иврит как официальный язык наряду с арабским и английским."},{"id":"212","text":"И добавил к названию “Палестины” две буквы, Алиф и Йа,"},{"id":"213","text":"сокращение от Эрец Исраэль,"},{"id":"214","text":"то есть Земля Израиля."},{"id":"215","text":"“Герберт Самуэль тот, кто юридически создал Израиль под руководством британского мандата.","voiceName":"Доктор Салман Абу Ситта, автор книги “Атлас Палестины 48”"},{"id":"216","text":"Во-первых, было принято около сотни законов, которые позволяли арабским землям переходить в руки евреев."},{"id":"217","text":"Во-вторых, он позволил евреям иметь отдельную систему образования."},{"id":"218","text":"Создал ядро министерства энергетики и они создали отдельные энергостанции."},{"id":"219","text":"Создал министерство общественных работ и министерство водных ресурсов."},{"id":"220","text":"А самое главное, что сделала Англия - это позволила иметь свою отдельную армию."},{"id":"221","text":"Я говорю о 1920 годе”."},{"id":"222","text":"Герберт Самуэль сам говорил: “Я буду работать над воплощением духа","voiceName":"Юсуф Хиджази, журналист и исследователь из Газы"},{"id":"223","text":"обещания Бальфура”. Бен Гурион говорил про него: “истинный еврей”,"},{"id":"224","text":"Вейцман говорил: “мы - его глаза”."},{"id":"225","text":"Британия предоставила евреям средства для переселения.","voiceName":"Журналист Дэвид Хирст, специализирующийся на ближневосточной тематике"},{"id":"226","text":"Их переселение было бы невозможно без Британии."},{"id":"227","text":"Решающей битвой на первой стадии был вход евреев в Палестину и овладение землями."},{"id":"228","text":"Евреи бы не смогли это сделать без поддержки британского правительства”."},{"id":"229","text":"“Британский мандат сделал важнейшую вещь: всё общественное, бесхозное и принадлежащее губернатору","voiceName":"Махди Абдуль-Хади, глава Палестинского Академического общества по международным делам"},{"id":"230","text":"немедленно передал во владение сионистскому движению в поселениях”."},{"id":"231","text":"“Британская власть раздавала земли евреям и они там строили кибуцы и прочее.","voiceName":"Мусаллам Бсейсо, журналист, корреспондент Ассошиэйтед пресс в Палестине в 1948"},{"id":"232","text":"Мы видели всё это, но не осознавали."},{"id":"233","text":"Это было время моего поколения”."},{"id":"234","text":"“Они защищали еврейское агентство, которое было реальной властью под тенью мандата","voiceName":"Доктор Анис Саиг, главный редактор “Энциклопедии Палестины”"},{"id":"235","text":"и оказывали всяческую военную и материальную помощь и помогали им оставаться незамеченными."},{"id":"236","text":"Это была еврейская хитрость,"},{"id":"237","text":"британский замысел"},{"id":"238","text":"и в какой-то степени, мне жаль это говорить,"},{"id":"239","text":"глупость или наивность палестинских арабов”."},{"id":"240","text":"После поддержки британцами евреев"},{"id":"241","text":"и возрастания числа крестьян, изгнанных с посевных земель,"},{"id":"242","text":"в сельских районах стали появляться повстанческие группы."},{"id":"243","text":"И в 1921 году были организованы массовые демонстрации против сионистской иммиграции."},{"id":"244","text":"Палестинское политическое управление было традиционным, должности передавались по наследству."},{"id":"245","text":"Так муфтий Иерусалима Амин Хусейни наследовал этот пост в возрасте 25 лет"},{"id":"246","text":"который был преемником своего отца Тахира Хусейна, тоже муфтия Иерусалима."},{"id":"247","text":"Это руководство отправилось в начале 20-х годов, в числе продолжающихся делегаций в Лондон, для обсуждения палестинского вопроса."},{"id":"248","text":"“Палестинское национальное движение работало над тем, чтобы собрать лето и зиму под одной крышей. Они хранили дружбу с Британией","voiceName":"Юсуф Хиджазий, журналист и исследователь из Газы"},{"id":"249","text":"Они хранили дружбу с Британией и враждовали с сионистским движением, что было нелепо”."},{"id":"250","text":"“Палестинская элита была очень тонкой, в плане численности и образованности. Они столкнулись с элитой империй,","voiceName":"Доктор Азми Бишара, арабский мыслитель"},{"id":"251","text":"Они столкнулись с элитой империй, ведь евреи начала 20-го века были элитой империй”."},{"id":"252","text":"Практически во всех областях","voiceName":"Доктор Хилел Коэн, историк и исследователь в Еврейском университете"},{"id":"253","text":"и на любой встрече палестинских организаций, основанных в тот период,"},{"id":"254","text":"например Мирный христианский комитет, Высший исламский совет, Исламская организация и других организаций,"},{"id":"255","text":"в течении нескольких часов после мероприятий,"},{"id":"256","text":"британцы предоставляли евреям доклад о происходившем там со всеми подробностями, вроде: “эти вам враги"},{"id":"257","text":"с этими можно работать, а этих можно купить”."},{"id":"258","text":"На земле продолжалась работа по достижению целей британского мандата."},{"id":"259","text":"На заседании Лиги наций Британия докладывала"},{"id":"260","text":"об успехах 1920-го года, который недавно завершился:"},{"id":"261","text":"(1) 33801 еврею была оказана помощь в переселении и предоставлено палестинское гражданство."},{"id":"262","text":"Это в 3 раза больше, чем в прошлом году."},{"id":"263","text":"(2) основано 13 новых поселений."},{"id":"264","text":"(3) Основан гистадрут - профсоюз еврейских рабочих под руководством Давида Бен-Гуриона."},{"id":"265","text":"(4) Еврейскому городу Тель-Авиву предоставлено местное самоуправление."},{"id":"266","text":"(5) Состоялось официальное открытие еврейского университета в присутствии генерала Эдмунда Алленби, британского губернатора Герберта Самуэля"},{"id":"267","text":"и почетного гостя Артура Бальфура и главы Всемирной сионистской организации"},{"id":"268","text":"Хаима Вейцмана."},{"id":"269","text":"Бальфур остановился в гостях у Вейцмана и посетил многие еврейские колонии."},{"id":"270","text":"Самуэль, Бальфур и Алленби встретились в Иерусалиме, чтобы обсудить следующий шаг."},{"id":"271","text":"Палестинцы бастовали, развесили черные флаги"},{"id":"272","text":"и назвали посещение Бальфура зловещим."},{"id":"273","text":"На известном фото на женщине из Хеврона (Аль-Халила) одето три платка на голове"},{"id":"274","text":"на женщине из Иерусалима - один платок, из под которого видна большая часть лица,"},{"id":"275","text":"и местная христианка без платка."},{"id":"276","text":"Некоторых из них отвели к верховному комиссару с возмущением."},{"id":"277","text":"С другой стороны, Вейцман поздравил Самуэля с достижением успеха"},{"id":"278","text":"Сионистская организация чествовала Самуэля в 1925 году"},{"id":"279","text":"за завершение первого этапа создания национального дома еврейского народа."},{"id":"280","text":"Сионистское движение показывает по всему миру пропагандистские фильмы."},{"id":"281","text":"Среди них этот для франкоязычных."},{"id":"282","text":"В нем появляется название “Земля Израиля”"},{"id":"283","text":"над картой Палестины"},{"id":"284","text":"и отмечены территории, которыми сионисты завладели"},{"id":"285","text":"к 1925 году"},{"id":"286","text":"и говорится, что это 177 тыс. гектаров."},{"id":"287","text":"Также показывается плановая цифра  "},{"id":"288","text":"на следующий, 1925 год, что для Еврейского земельного фонда важно заполучить 1 млн. гектаров."},{"id":"289","text":"Летом 1929 Еврейское агентство организовало массовую сионистскую молитву у стены Аль-Бурак (стена плача) с просьбой возобновить строительство храма."},{"id":"290","text":"Вспыхнул народный бунт, известный под названием “восстание Аль-Бурак”,"},{"id":"291","text":"которое запустил палестинский крестьянин Фархан Ас-Сагди из деревни Аль-Мазар провинции Дженин."},{"id":"292","text":"Верховный британский комиссар Джон Роберт Чанселлор выступил"},{"id":"293","text":"с решительным заявлением,"},{"id":"294","text":"что участники восстания понесут суровое наказание"},{"id":"295","text":"Ас-Сагди был задержан с десятками своих товарищей."},{"id":"296","text":"Среди них был уроженец Цфата, выпускник Американского университета в Бейруте, Фуад Хасан Хиджазий,"},{"id":"297","text":"уроженец Хеврона (Аль-Халила) Ата Ахмад Аз-Зир,"},{"id":"298","text":"и Мухаммад Халил Джамджум, фотографию которого мы не нашли."},{"id":"299","text":"Здесь, в тюрьме замка Акки эти трое сидели, приговоренные к смертной казни,"},{"id":"300","text":"а арабские старейшины просили смягчить им наказание. Но британские власти 17 июня 1930 исполнили приговор через повешение."},{"id":"301","text":"Могилы этих трех мучеников сохранились, и мы можем прочитать на них:"},{"id":"302","text":"“У нас, в конце нашей жизни, надежда только на правителей арабов и мусульман."},{"id":"303","text":"Мы живем и умрем ради арабов”."},{"id":"304","text":"За 10 лет британского мандата численность евреев увеличилась более чем в 3 раза"},{"id":"305","text":"и достигла 175 тыс."},{"id":"306","text":"Сионисты всего мира гордились своим успехом."},{"id":"307","text":"“Я пришел сегодня, чтобы спросить вас, какую позицию вы займете из возможных?","voiceName":"Стивен Вайз, американский сионист, Нью-Йорк, 1931 год"},{"id":"308","text":"Я хочу сказать Англии, несмотря на то, что я американский еврей,"},{"id":"309","text":"но я вот уже долгое время восхищаюсь Великобританией,"},{"id":"310","text":"и хотел бы сказать ей, если бы смог:"},{"id":"311","text":"существование арабской Палестины - это гибель для Великобритании и опасность для всего мира. Но существование еврейской Палестины - это польза для Великобритании и благословение для всего мира”."},{"id":"312","text":"Дэвид Ллойд Джордж под аплодисменты Вейцмана"},{"id":"313","text":"работает над передачей собственности Палестины сионисту."},{"id":"314","text":"Как сказал ваш президент,","voiceName":"Дэвид Ллойд Джордж, бывший премьер-министр Британии, на сионистском торжественном ужине в Лондоне в 1931 году"},{"id":"315","text":"прошло около 16 лет,"},{"id":"316","text":"как он завербовал меня в сионистское движение."},{"id":"317","text":"И Палестина превратилась из бесплодных малярийных болот в процветающие колонии. Там возделывают земли, в которых вода пропадала впустую с начала времен”."},{"id":"318","text":"Свободная пустыня, без растительности, без жизни, такова Палестина,"},{"id":"319","text":"так представляли ее в Европе,"},{"id":"320","text":"но такой ли была она в 1930х годах."},{"id":"321","text":"Клянусь Аллахом, посевы были такой высоты, что через них невозможно было пройти, выше меня ростом.","voiceName":"Хосни Мухаммад Самадига, уроженец Саббарина, провинция Хайфа, проживающий в лагере беженцев Джалазон в Рамалле"},{"id":"322","text":"Зелень заполняла равнины. Когда утром выходишь в сад, запахи зелени повсюду, цветы огурцов, дынь и томатов благоухают."},{"id":"323","text":"Ничто не заменит мне Хайфу, я хорошо её помню, она цвела жизнью”."},{"id":"324","text":"“Для меня Тиберия - это лучший город и лучшее место на земле."},{"id":"325","text":"Иисус Христос не останавливался в городе Тиберии,"},{"id":"326","text":"Тиберии, но был во всех её окрестностях"},{"id":"327","text":"и совершил там большинство своих чудес, ходил по воде”."},{"id":"328","text":"Дэвид Ллойд Джордж, Бальфур и Черчилль были заинтересованы называть палестинцев “магометанами”."},{"id":"329","text":"“Они не рассматривали палестинцев как связанных с арабской или с местной национальной группой.","voiceName":"Доктор Илан Паппе, израильский историк, университет Экстер, Британия"},{"id":"330","text":"А видели в них только религиозную группу."},{"id":"331","text":"Например, они игнорировали,"},{"id":"332","text":"что христиане и мусульмане в Палестине образовали новую национальную,"},{"id":"333","text":"а не религиозную идентичность”."},{"id":"334","text":"“На востоке нет христианского континента (вроде Европы). Поэтому колонизаторы, рассматривающие Ближний Восток через призму христианской теологии,","voiceName":"Доктор Азми Бишара, арабский мыслитель"},{"id":"335","text":"попали в тупик, что и привело их к принятию сионизма."},{"id":"336","text":"Факт существования восточных христиан спутал бы все планы колонизаторов, поэтому надо было скрыть этот факт”."},{"id":"337","text":"В 1933 году в Палестине вспыхнули народные волнения, в которых участвовали и мужчины и женщины."},{"id":"338","text":"Британцы подавили их. Тысячи людей были арестованы."},{"id":"339","text":"Многие были ранены и убиты. В том числе бывший глава городского управления Иерусалима, Муса Казим Аль-Хусейни."},{"id":"340","text":"Его избили британские солдаты на демонстрации в Иерусалиме, после чего он умер от полученных ран."},{"id":"341","text":"Палестинский полицейский Мухсин Тавфик написал рапорт"},{"id":"342","text":"против действий своего начальника, британского офицера Джорджа Фарадея,"},{"id":"343","text":"выпустившего 25 пуль в палестинских демонстрантов в Яффе."},{"id":"344","text":"Против него было направлено несколько жалоб."},{"id":"345","text":"Ответ британского правительства пришел спустя 4 года."},{"id":"346","text":"Фарадей был награжден орденом королевской полиции за действия в Палестине.  "},{"id":"347","text":"“Британия не хотела выполнять гражданские требования палестинцев, не хотела рассматривать их как национальную группу,","voiceName":"Доктор Мустафа Кабаха, исследователь истории Палестины"},{"id":"348","text":"наделенную правами и не взаимодействовала с ними как с группой."},{"id":"349","text":"В то время как с еврейским комитетом она сотрудничала как с национальной группой."},{"id":"350","text":"Всё это привело к осложнению ситуации и стычкам с Британией”."},{"id":"351","text":"В архивах есть только фотографии некоторых палестинских демонстраций,"},{"id":"352","text":"но нет видео,"},{"id":"353","text":"в то время как деятельность сионистов и британцев полностью снята на видео."},{"id":"354","text":"Тель-Авив поднимает знамя сионизма в середине 1930х,"},{"id":"355","text":"экспортирует хлопок и алмазы с еврейских фабрик,"},{"id":"356","text":"продолжает принимать еврейских переселенцев и строить поселения."},{"id":"357","text":"Во время работы над этим фильмом мы обнаружили попытки сионистской пропаганды стереть архивные записи.","voiceName":"Журналистка Рован Домен"},{"id":"358","text":"Потребовались месяцы исследований в британском архиве, здесь в Лондоне,"},{"id":"359","text":"чтобы найти фотографии и аудиозаписи некоторых скрываемых фактов."},{"id":"360","text":"“Большинство палестинцев из числа лидеров, журналистов,","voiceName":"Доктор Илан Паппе, израильский историк, университет Экстер, Британия"},{"id":"361","text":"образованных людей даже не представляли себе,"},{"id":"362","text":"что сионистское движение настолько большое, что скоро выгонит их из Палестины.”"},{"id":"363","text":"Число еврейских иммигрантов в Палестину выросло"},{"id":"364","text":"от 4 тыс. в 1931 до 9,5 тыс. в 1932,"},{"id":"365","text":"до 30 тыс. в 1933,"},{"id":"366","text":"и достигло своего максимума 62 тыс. в 1935."},{"id":"367","text":"В том же году палестинский поэт Абдурахим Махмуд"},{"id":"368","text":"выступил перед эмиром Саудом Ибн Абдульазизом (будущим королем Саудовской Аравии), посетившим Иерусалим, с касыдой, в которой говорилось:  "},{"id":"369","text":"“Мечеть Аль-Акса…"},{"id":"370","text":"Ты пришел навестить ее?"},{"id":"371","text":"Или пришел ты перед потерей, попрощаться с ней?”"},{"id":"372","text":"Эти земли конфискованы,","voiceName":"Махди Абдуль-Хади, глава Палестинского Академического общества по международным делам"},{"id":"373","text":"обещание выполняется уже"},{"id":"374","text":"уже без участия британского правительства, при котором были подавления, репрессии и жестокие законы. Израиль продолжает это и по сей день:"},{"id":"375","text":"аресты, выселение и притеснение."},{"id":"376","text":"В 1935 году выступил Иззуддин Аль-Кассам”."},{"id":"377","text":"“Шахид Иззуддин Аль-Кассам олицетворял ситуацию 30-х годов.","voiceName":"Маджид Аз-Зир, генеральный директор центра возврата палестинцев"},{"id":"378","text":"Он был арабом из Сирии,"},{"id":"379","text":"религиозным, любил мечеть,"},{"id":"380","text":"был организован, упорен и решителен в сопротивлении оккупантам”."},{"id":"381","text":"“Когда Аль-Кассам написал палестинскому руководству в Иерусалиме, советуясь насчет начала восстания, они ответили ему: “Сейчас не подходящее время для объявления восстания.","voiceName":"Юсуф Хиджазий, журналист и исследователь из Газы"},{"id":"382","text":"И народ не созрел для этого. Мы видим, что они надеются на достижение наших прав через политические переговоры”."},{"id":"383","text":"Они помешали шейху Иззуддину Кассаму объявить вооруженное восстание. Вскоре британские войска блокировали его в лесу в окрестностях деревни Ябад, провинции Дженина,"},{"id":"384","text":"обстреляли его группу из самолетов и артиллерии. В результате погиб шейх и его товарищи Юсуф Аз-Зибави и Саид Ханафи Атия."},{"id":"385","text":"“Историки мало интересуются движением Аль-Кассама.","voiceName":"Доктор Мустафа Кабаха, исследователь истории Палестины"},{"id":"386","text":"И мы мало знаем о нём, потому что оно окутано тайной"},{"id":"387","text":"Шейх Иззудин уделял большое внимание секретности."},{"id":"388","text":"В дело были посвящены лишь немногие из его студентов”."},{"id":"389","text":"“Дух Иззуддина Аль-Кассама витал над Палестиной,","voiceName":"Джавдат Аль-Хадж Мухаммад, сын шахида Абдурахима Аль-Хадж Мухаммада"},{"id":"390","text":"многие палестинцы были под его влиянием."},{"id":"391","text":"В рядах палестинского народа возникло предвкушение бунта против британских колонизаторов”."},{"id":"392","text":"Палестинское политическое руководство продолжало диалог с англичанами,"},{"id":"393","text":"несмотря на давление народа. В 1935 году палестинский поэт Ибрахим Тукан написал “Слово к руководству”:"},{"id":"394","text":"“Вы искренние патриоты,"},{"id":"395","text":"Вы несете бремя проблемы"},{"id":"396","text":"В наших руках лишь остаток страны"},{"id":"397","text":"Так отдыхайте, чтобы не улетел и этот остаток”."},{"id":"398","text":"В Яффе началась стачка","voiceName":"Юсуф Хиджази, журналист и исследователь из Газы"},{"id":"399","text":"и 19 апреля 1936 город был освобожден."},{"id":"400","text":"Руководство палестинского национального движения состояло из 6 партий, находящихся в клановом противостоянии друг с другом"},{"id":"401","text":"Спустя 6 дней после начала восстания, 25 апреля 1936, они создали Верховный арабский комитет, который фактически управлял революцией."},{"id":"402","text":"“Главная претензия арабов направлена против политики британского правительства в Палестине.","voiceName":"Выступающий от имени Верховного арабского комитета в 1936"},{"id":"403","text":"Ее продолжение приведет к вытеснению арабов и их замене евреями."},{"id":"404","text":"Вопреки всем принципам, британское правительство навязывает декларацию Бальфура, ненавистную для всех арабов Ближнего Востока"},{"id":"405","text":"и покровительствует созданию еврейского национального дома и умышленно игнорируют гражданские права неевреев."},{"id":"406","text":"Поэтому арабы решили устроить всеобщую забастовку,"},{"id":"407","text":"до полной остановки еврейской иммиграции"},{"id":"408","text":"и полного изменения текущей британской политики”."},{"id":"409","text":"“Эта забастовка была крупнейшей в истории.","voiceName":"Мусаллам Бсейсо, журналист, корреспондент Ассошиэйтед пресс в Палестине в 1948"},{"id":"410","text":"Каждый палестинец в 1936-м бастовал."},{"id":"411","text":"Это было следствием борьбы против британского мандата,"},{"id":"412","text":"поддерживающего евреев во всех начинаниях”."},{"id":"413","text":"Это была всеобщая забастовка, потрясшая британский мандат."},{"id":"414","text":"Британские власти приняли решительные карательные меры."},{"id":"415","text":"Арестовывали всех, кого подозревали в связях с повстанцами,"},{"id":"416","text":"взрывали их дома. В Яффо разрушили более 200 домов для коллективного устрашения, а затем и в других городах и деревнях."},{"id":"417","text":"Британцы настаивали, что разрушение домов оправдано для прекращения восстания."},{"id":"418","text":"Что вы помните об англичанах в стране? Что они делали?","voiceName":"Журналистка Рован Домен"},{"id":"419","text":"“Англичане установили свой пост на дороге.","voiceName":"Сами Камал Абдураззак, уроженец Таршиха, провинция Акка"},{"id":"420","text":"И мой дядя со своими рабочими проходил мимо них по пути на работу."},{"id":"421","text":"Как-то англичане сказали ему: “зайди со своими работниками и приберись у нас”. Он отказался."},{"id":"422","text":"И офицер по имени Раймонд сильно избил его."},{"id":"423","text":"Они продолжали его бить так, что он умер. Мы отвезли его в госпиталь “Хамза” в Хайфе, для операции. В его груди были разорваны ткани и он умер”."},{"id":"424","text":"“Они били нас”.","voiceName":"Хосни Мухаммад Самадига, уроженец Саббарина, провинция Хайфа, проживающий в лагере беженцев Джалазон возле Рамаллы"},{"id":"425","text":"Что они делали?"},{"id":"426","text":"Они избивали мужчин и увозили их в тюрьму в Тулькарме или Джурджур возле Бен-Ямина"},{"id":"427","text":"и заключали их под стражу на месяц или два и заставляли работать на каменоломне. Их отводили в деревню Нур-Шамс на карьеры или для выполнения других тяжелых работ”."},{"id":"428","text":"Если вы прочитаете мемуары Бен-Гуриона,","voiceName":"Доктор Махди Абдуль-Хади, глава Палестинского Академического общества по международным делам"},{"id":"429","text":"в разгар восстания 1936-го он бросил вызов арабской делегации"},{"id":"430","text":"во главе с Мусой Алами"},{"id":"431","text":"на переговорах, организованных верховным комиссаром Артуром Уокопом."},{"id":"432","text":"Он сказал им: “У нас есть реальное присутствие. Запад и Британия не могут сказать нам нет. А кто вы в Британии?”"},{"id":"433","text":"Во время восстания Давид Бен-Гурион, глава еврейского агентства, беседовал с верховным комиссаром Артуром Уокопом"},{"id":"434","text":"о возможности переселения арабских крестьян, вытесненных с территорий еврейских поселений"},{"id":"435","text":"на востоке Иордана."},{"id":"436","text":"Уокоп ответил,"},{"id":"437","text":"что это хорошая идея."},{"id":"438","text":"Конец первой части."}]},"id":"001"}]}